export interface paciServiceInput {
    civilid?: string;
    lang?: string;
    serialno?: string;
}

export const getPaciServiceInfo = async (input: paciServiceInput): Promise<paciServiceObj> => {
    try {
        let response = await fetch(PACI_SERVICE_URL + "lang=" + input.lang + "&civilid=" + input.civilid + "&serialno=" + input.serialno, {
            method: "Get",
            headers: { 'Content-Type': 'application/json' },
        });
        let res: paciServiceObj = await response.json();
        return res;
    } catch (error) {
        return  {};
    }

}



