import * as React from 'react';
import { useSelector, useDispatch } from 'react-redux';
import { RootState } from "../../../State/rootReducer"
import { TableBootStrap, columnsBootStrap } from "../../../Controls/Datatables"
import Layout from "../../../Layout/ClientSec/Layout";
import NoData from "../../Common/NoData";
import { IRequests, IRequestsState } from '../../../State/ManageRequests/types';
import * as actions from "../../../State/ManageRequests/actions"
import * as Types from "../../../State/ManageRequests/types"
import { Alert } from 'reactstrap';
import { types } from 'util';
import * as lookUpActions from "../../../State/lookUps/action";
import { useForm, Controller } from "react-hook-form";
import Select from 'react-select';
import ReactDatePicker from "react-datepicker";
import { SelectOptions } from "../../../types/UIRelated"
import moment from 'moment'
import { ErrorMessages } from "../../../types/Enums"
import { ReactDatePicker as CustomReactDatePicker } from "../../../Controls/CustomDatePicker";

export interface IFormDataAR {
  ApplicationNumber?: number;
  ApplicationType?: SelectOptions;
  UserType?: SelectOptions;
  ApplicationStatus?: SelectOptions;
  civilID?: number;
  userName?: string;
  EmployeeNumber?:string;
  submittedBy?: string;
  fromDate?: Date;
  toDate?: Date;

}
export interface ApiInputAR {

  ApplicationNumber?: string;
  ApplicationTypeId?: string;
  UserTypeId?: string;
  CivilId?: string;
  ApplicationStatusId?: string;
  EmployeeName?: string;
  EmployeeNumber?: string;
  submittedBy?: string;
  FromDate?: string;
  ToDate?: string;
  userId?:string;
}
export default function ApplicationsReports() {

  const RequestsState: IRequestsState = useSelector<RootState, RootState["manageRequest"]>(state => state.manageRequest);
  const { userInfo } = useSelector<RootState, RootState["login"]>(state => state.login);
  const LookUpState = useSelector<RootState, RootState["lookUp"]>(state => state.lookUp);
  const { AppTypes, UserTypes } = LookUpState;
  const AppStatus = [{ value: "1", label: " " }, { value: "2", label: "" }, { value: "3", label: " " }, { value: "5", label: "   " }];
  const { errors, handleSubmit, register, setValue, reset, control, getValues } = useForm<IFormDataAR>(

  );
  const { IRequests } = RequestsState;
  const { SelectedRequest } = RequestsState;
  const [alertVisible, setAlertVisible] = React.useState(false);
  const [alertType, setAlertType] = React.useState("");
  const [IRequests_, setIRequests_] = React.useState(RequestsState?.IRequests);
  let dispatch = useDispatch();

  //Table boot strap input {columns,handel pop up function its (ref) function}
  type handleRowFunctions = React.ElementRef<typeof TableBootStrap>;
  const handleRowFunctionsref = React.useRef<handleRowFunctions>(null);

  let columnsBootStrap_: columnsBootStrap = { columns: [], data: null, tableName: "", hasSelection: true };
  columnsBootStrap_.columns = [


    {
      text: " ",
      dataField: "applicationNumber"
    },
    {
      text: " ",
      dataField: "civilId"
    },
    {
      text: " ",
      dataField: "userName"
    },
    {
      text: "",
      dataField: "nationalityName"
    },
    {
      text: " ",
      dataField: "applicationStatusName",
      formatter:(row:any,cell:any)=> <td className="h-100 w-100 d-inline-block"  style={{background:cell?.applicationStatusId==1?"#28a745":cell?.applicationStatusId==2?"#dc3545":cell?.applicationStatusId==3?"#ffc107":"#0260ae"}}>{cell.applicationStatusName}</td>
    },
    {
      text: " ",
      dataField: "applicationTypeName"
    },
    {
      text: "",
      dataField: "remark"
    },
    {
      text: " ",
      dataField: "applicationDate",
      formatter: (row: any, cell: any) => <span> {new Date(cell.applicationDate).toLocaleDateString('ar-EG-u-nu-latn', { weekday: 'long', year: 'numeric', month: 'short', day: 'numeric' })}   </span>
    },
    {
      text: "",
      formatter: (row: any, cell: any) => <button className="btn btn-primary btn-sm  rounded-circle" onClick={() => handleRowFunctionsref.current?.handlePopUp(cell)}><i className="fa fa-print" aria-hidden="true"></i></button>
    },
    {
        text: "",
        formatter: (row:any,cell:any) => <button  className="btn btn-primary btn-sm  rounded-circle"   onClick={()=>handleRowFunctionsref.current?.handlepopUpComments(cell)}><i className="fa fa-comments"aria-hidden="true"></i></button>
    }

  ];
  React.useEffect(() => {
    dispatch({ type: Types.Clear });
    dispatch(lookUpActions.getAppTypesRequest())
    dispatch(lookUpActions.getUserTypesRequest());
  }, []);


  React.useEffect(() => {
    setIRequests_(RequestsState?.IRequests)
  }, [RequestsState]);


  const submit = (formInput: IFormDataAR) => {
    let input: ApiInputAR = {
      ApplicationNumber: "0",
      CivilId: "0",
      ApplicationTypeId: "0",
      ApplicationStatusId: "0",
      UserTypeId: "0",
      EmployeeName: "",
      EmployeeNumber: "",
      submittedBy: "",
      ToDate: moment(new Date("01/01/2080")).format("MM/DD/YYYY HH:mm"),
      FromDate: moment(new Date("01/01/1980")).format("MM/DD/YYYY HH:mm"),
      userId:userInfo?.userId as unknown as string

    };


    (formInput.ApplicationNumber?.toString() != '' && formInput.ApplicationNumber?.toString() != 'NaN' && formInput.ApplicationNumber?.toString() != undefined) ? input.ApplicationNumber = formInput.ApplicationNumber as unknown as string : input.ApplicationNumber = "0";
    (formInput.civilID?.toString() != '' && formInput.civilID?.toString() != 'NaN' && formInput.civilID?.toString() != undefined) ? input.CivilId = formInput.civilID as unknown as string : input.CivilId = "0";
    formInput.ApplicationType?.value != undefined ? input.ApplicationTypeId = formInput.ApplicationType?.value : input.ApplicationTypeId = "0";
    formInput.UserType?.value != undefined ? input.UserTypeId = formInput.UserType?.value : input.UserTypeId = "0";
    
    formInput.ApplicationStatus?.value != undefined ? input.ApplicationStatusId = formInput.ApplicationStatus?.value : input.ApplicationStatusId = "0";
    formInput.userName != undefined ? input.EmployeeName = formInput.userName : input.EmployeeName = "";
    formInput.EmployeeNumber != undefined ? input.EmployeeNumber = formInput.EmployeeNumber : input.EmployeeNumber = "";
    
    formInput.submittedBy != undefined ? input.submittedBy = formInput.submittedBy : input.submittedBy = "";
    formInput.fromDate != undefined ? input.FromDate = moment(formInput.fromDate).format("MM/DD/YYYY HH:mm") : input.FromDate = moment(new Date("01/01/1980")).format("MM/DD/YYYY HH:mm");
    formInput.toDate != undefined ? input.ToDate = moment(formInput.toDate).format("MM/DD/YYYY HH:mm") : input.ToDate = moment(new Date("01/01/2080")).format("MM/DD/YYYY HH:mm");
    dispatch(actions.CreateRequestAdminInwardSearch(input));
    formInput.fromDate = undefined;
    formInput.toDate = undefined;

  }
  const handelRest = (e: any) => {
    e.preventDefault();
    reset();
    reset({
      ApplicationNumber: undefined,
      ApplicationStatus: { value: "0", label: "     " },
      ApplicationType: { value: "0", label: "     " },
      UserType:{ value: "0", label: "     " },
      civilID: undefined,
      userName: undefined,
      EmployeeNumber:undefined,
      submittedBy: undefined,
      toDate: new Date(),
      fromDate: new Date("01/01/2019")
    });
    dispatch({ type: Types.Clear });
    let s = getValues("toDate");
    let ss = getValues("ApplicationStatus");

  }
  return (
    <>
      <div className="row justify-content-center">
        <div className="col-xl-12 col-lg-12 col-md-12">
          <div className="card o-hidden border-1 shadow my-5 animate__animated animate__fadeIn " style={{ border: '1px solid rgb(189, 189, 189)' }}>
            <div className="card-header bg-dark">
              <div className="text-center">
                <h1 className=" text-gray-900 mb-1 shorooq gold" style={{ fontSize: 28 }}> 
                </h1>
              </div>
            </div>
            <div className="card-body p-5 ">
              <div className="p-5">
                <form className="user" onSubmit={handleSubmit(submit)}>
                  <div className="row">
                    <div className="col-sm-6">
                      <div className="form-group row">
                        <label className="col-sm-6 col-form-label">
                          </label>
                        <div className="col-sm-6">
                          <input type="text" name="ApplicationNumber" ref={register({ valueAsNumber: true })} className="form-control form-control-user" />
                          {errors?.ApplicationNumber?.type === "required" &&
                            <span className="text-danger">{ErrorMessages.valueAsNumber}</span>}
                        </div>
                      </div>
                      <div className="form-group row">
                        <label className="col-sm-6 col-form-label">
                          </label>
                        <div className="col-sm-6">
                          <Controller
                            control={control}
                            name="UserType"
                            placeholder="     "
                            options={UserTypes}
                            as={Select}
                          />
                        </div>
                      </div>
                      <div className="form-group row">
                        <label className="col-sm-6 col-form-label">
                          </label>
                        <div className="col-sm-6">
                          <Controller
                            control={control}
                            name="ApplicationType"
                            placeholder="     "
                            options={AppTypes}
                            as={Select}
                          />
                        </div>
                      </div>
                      <div className="form-group row">
                        <label className="col-sm-6 col-form-label">
                          </label>
                        <div className="col-sm-6">
                          <Controller
                            control={control}
                            name="ApplicationStatus"
                            placeholder="     "
                            options={AppStatus}
                            as={Select}
                          />
                        </div>
                      </div>
                    </div>
                    <div className="col-sm-6">
                      <div className="form-group row">
                        <label className="col-sm-6 col-form-label">
                          </label>
                        <div className="col-sm-6">
                          <input name="civilID" ref={register({ valueAsNumber: true })} type="text" className="form-control form-control-user" />
                          {errors?.civilID?.type === "valueAsNumber" &&
                            <span className="text-danger">{ErrorMessages.valueAsNumber}</span>}
                        </div>
                      </div>
                      <div className="form-group row">
                        <label className="col-sm-6 col-form-label">  </label>
                        <div className="col-sm-6">
                          <input name="EmployeeNumber" ref={register} type="text" className="form-control form-control-user" />
                        </div>
                      </div>
                      <div className="form-group row">
                        <label className="col-sm-6 col-form-label"> </label>
                        <div className="col-sm-6">
                          <input name="userName" ref={register} type="text" className="form-control form-control-user" />
                        </div>
                      </div>
                      <div className="form-group row">
                        <label className="col-sm-6 col-form-label">   </label>
                        <div className="col-sm-6">
                          <input name="submittedBy" ref={register} type="text" className="form-control form-control-user" />
                        </div>
                      </div>
                      <div className="form-group row">
                        <label className="col-sm-3 col-form-label"> </label>
                        <div className="col-sm-3">
                          <Controller
                            ref={register}
                            control={control}
                            name="fromDate"
                            render={({ value, onBlur, onChange }) => (
                              <CustomReactDatePicker required={false} onChange={onChange} onBlur={onBlur} ss={value} />

                            )}
                          />

                        </div>
                        <label className="col-sm-3 col-form-label"> </label>
                        <div className="col-sm-3">
                          <Controller
                            ref={register}
                            control={control}
                            name="toDate"
                            render={({ value, onBlur, onChange }) => (
                              <CustomReactDatePicker required={false} onChange={onChange} onBlur={onBlur} ss={value} />
                            )}
                          />
                        </div>
                      </div>
                    </div>
                  </div>
                  <hr />
                  {/* ################### form- row-002 #################*/}
                  {/* ################# submit btn ##################### */}
                  <div className="row justify-content-center">
                    <button type="submit" className="btn btn-primary btn-user shorooq mr-3 ml-3 mb-3"> <i className="fas fa-search" />
                      &nbsp; </button>
                    <button onClick={(e) => handelRest(e)} className="btn btn-primary btn-user shorooq mr-3 ml-3 mb-3"> <i className="fas fa-search" />
                      &nbsp;  </button>
                  </div>
                  {/* ################# end submit btn ##################### */}
                </form>
                <Alert color={alertType} isOpen={alertVisible} >
                  {RequestsState.message}
                </Alert>
                <button onClick={() => handleRowFunctionsref.current?.handlePopUpAll()} disabled={RequestsState?.IRequests.length == 0} className="btn btn-primary btn-user shorooq mr-3 ml-3 mb-3">  </button>
                {(userInfo?.userId != 0 && IRequests_.length != 0) && <TableBootStrap hasSelection={true} ref={handleRowFunctionsref} data={IRequests_} columns={columnsBootStrap_.columns} tableName="" />}
                {(userInfo?.userId == 0 || IRequests_.length == 0) && <NoData Message="      " />}
              </div>
            </div>
          </div>
        </div>
      </div>



    </>
  );
}
