

import { paciServiceObj } from '../../Services/paciService';
import * as actions from './action'
import * as types from "./types"

const initialState: types.IState = {
    aDDITIONAL_PARAM_1Field: undefined,
    aDDITIONAL_PARAM_2Field: undefined,
    araB_NAME_1: undefined,
    araB_NAME_2: undefined,
    araB_NAME_3: undefined,
    araB_NAME_4: undefined,
    bIRTH_DATEField: undefined,
    cARD_SERIAL_NOFiel: undefined,
    civno: undefined,
    dISTRICTNAMEField: undefined,
    fullNameAr: undefined,
    fullNameEn: undefined,
    latiN_NAME_1: undefined,
    latiN_NAME_2: undefined,
    latiN_NAME_3: undefined,
    latiN_NAME_4: undefined,
    nATIONALITYField: undefined,
    pASSPORT_ISSUE_PLACEField: undefined,
    pASSPORT_NOField: undefined,
    sEXField: undefined,
    sPONSOR_NAMEField: undefined
}

export function paciReducer(
    state: types.IState = initialState, action: types.paciTypes): types.IState {
    switch (action.type) {

        case types.PACI_CLEAR:
            localStorage.setItem("paci", JSON.stringify({ ...initialState }));
            return initialState;


        case types.PACI_SUCCESS:
            localStorage.setItem("paci", JSON.stringify({ ...action.payload }));
            return {
                ...state,
                ...action.payload,
                fullNameAr: action.payload.araB_NAME_1 + " " + action.payload.araB_NAME_2 + "" + action.payload.araB_NAME_3 + "" + action.payload.araB_NAME_4,
                fullNameEn: action.payload.araB_NAME_1 + " " + action.payload.araB_NAME_2 + "" + action.payload.araB_NAME_3 + "" + action.payload.araB_NAME_4
            }

        default:
            let localStorageData: any = localStorage.getItem('paci');
            if (localStorageData) {
                localStorageData = JSON.parse(localStorageData);
                return {
                    ...localStorageData
                }
            }
            return state;
    }

}