import React, { useEffect, useState, useRef, forwardRef, useImperativeHandle } from 'react';
import DatePicker from "react-datepicker";


const yearsfn = function(startYear:any) {
    var currentYear = new Date().getFullYear(), years = [];
    startYear = startYear || 1980;  
    while ( startYear <= currentYear+10 ) {
        years.push(startYear++);
    }   
    return years;
}
interface Props {
  required:boolean
    onChange: any
    onBlur: any
    ss: any
  }
export const ReactDatePicker: React.FC<Props>=({required,onChange,onBlur,ss}) => {
    const [startDate, setStartDate] = useState(new Date());
    const years =yearsfn(1990-20);
    const months = ["", "" , "" , "" , "" , "" , "" , "" , "" , "" , "" , ""];
    React.useEffect(() => {
  
    }, [ss]);
  
  
    
    return (
      <DatePicker
        renderCustomHeader={({
          date,
          changeYear,
          changeMonth,
          decreaseMonth,
          increaseMonth,
          prevMonthButtonDisabled,
          nextMonthButtonDisabled
        }) => (
          <div
            style={{
              margin: 10,
              display: "flex",
              justifyContent: "center"
            }}
          >
          
            <select
              value={date.getFullYear()}
              onChange={({ target: { value } }) => changeYear(value as unknown as number)}
            >
              {years.map(option => (
                <option key={option} value={option}>
                  {option}
                </option>
              ))}
            </select>
  
            <select
              value={months[date.getMonth()]}
              onChange={({ target: { value } }) =>
                changeMonth(months.indexOf(value))
              }
            >
              {months.map(option => (
                <option key={option} value={option}>
                  {option}
                </option>
              ))}
            </select>
  
         
          </div>
        )}
        onBlur={onBlur}
        dateFormat="dd/MM/yyyy"
        placeholderText="dd/MM/yyyy "
        className="form-control form-control-user"
        required={required}
        selected={ss}
        
        onChange={onChange}
      />
    );
  };