import { GET_GENERALSETTINGS } from "./Urls";

export const getGeneralSettings = async () => {
    let response = await fetch(GET_GENERALSETTINGS, {
        method: "GET",
        headers: {
            'Content-Type': 'application/json'
        },
    });
    let res = await response.json()
    return res;
}

export const updateGeneralSettings = async (input: any) => {

    let response = await fetch(GET_GENERALSETTINGS, {
        method: "PUT",
        headers: {
            'Content-Type': 'application/json'
        },
        body: JSON.stringify({ ...input })
    });
    let res = await response.json()
    return res;
}


