
import {  IResponse,IApplicationType } from "../types"
import { NotificationSettings } from "./Urls";

export const getNotificationSettings= async (): Promise<IResponse<any>> => {
    let rtnObj: IResponse<IApplicationType[]>;
    try {
        let response = await fetch(NotificationSettings, {
            method: 'GET',
            headers: {
                'Content-Type': 'application/json'
            },
        });
        rtnObj = await response.json();
    } catch (e) {
        rtnObj = {
            status: 1,
            message: "ErrorOccured",
            response: e,
            hasError: true
        }
    }
    return rtnObj;
}

export const updateIsActive= async (name:string,isActive:boolean): Promise<IResponse<any>> => {
    let rtnObj: IResponse<IApplicationType>;
    try {
        let response = await fetch(`${NotificationSettings}/updateIsActive/${name}`, {
            method: 'PUT',
            headers: {
                'Content-Type': 'application/json'
            },
            body:JSON.stringify(isActive)
        });
        rtnObj = await response.json();
    } catch (e) {
        rtnObj = {
            status: 1,
            message: "ErrorOccured",
            response: e,
            hasError: true
        }
    }
    return rtnObj;
}