import React,{useState} from 'react'
import Layout from "../../Layout/Common/LayoutPublicSettings";
import { Button,Container,Row,Col } from 'react-bootstrap';


const RegisterSuccess = () => {

    return (
      <Layout>
        <main className="login-bg">
        <Container  style={{marginBottom: '80px'}}>
          {/* Outer Row */}
          <Row className=" justify-content-center">
            <Col xl={10} lg={12} md={9}>
              <div className="card o-hidden border-1 shadow my-5 animate__animated animate__backInDown " style={{border: '1px solid rgb(189, 189, 189)'}}>
                <div className="card-header bg-dark">
                  <div className="text-center">
                    <h1 className=" text-gray-900 mb-1 shorooq gold" style={{fontSize: '28px'}}>  </h1>
                  </div>
                </div>
                <div className="card-body p-0 ">
                  {/* Nested Row within Card Body */}
                  <div className="row">
                    <div className="col-lg-12">
                      <div className="p-5">
                        <form className="user">
                           <p>                 </p>
                        </form>
                      </div>
                    </div>
                  </div>
                </div>
              </div>
            </Col>
          </Row>
        </Container>
 
        </main>
     
      
     
     </Layout>
    )
}

export default RegisterSuccess
