import * as PageLoaderActions from "./types"


export const getPageLoaderRequest = (Status:boolean): PageLoaderActions.PageLoader_request_action_type => {
    return {
        type: PageLoaderActions.PAGELOADER_REQUEST,
        payload: Status
    }
}


export const getPageLoaderSuccess = (Status:boolean): PageLoaderActions.PageLoader_success_action_type => {
    return {
        type: PageLoaderActions.PAGELOADER_SUCCESS,
        payload: Status
    }
}


