import React, { SyntheticEvent, useContext, useEffect, useState } from "react";
import { authenticateResponse } from "../../types/userInfo";
import { useHistory } from "react-router-dom";
import FullPageLoader from "../../Controls/FullPageLoader";
import { Redirect, useParams, useLocation } from "react-router-dom";
import { getLocalStorage } from "../../Services/utils/localStorageHelper";
import Layout from "../../Layout/Common/LayoutPublicSettings";
import { useSelector, useDispatch } from 'react-redux';
import { RootState } from '../../State/rootReducer';
import { getloginRequest } from "../../State/login";
import { RequestClearRequest } from "../../State/newApp";
import generalSettingReducer, * as GeneralSettings  from '../../State/GeneralSettings'
import { getllogout } from "../../State/login";
import { ClearRequest } from "../../State/newApp";
import { RequestClear as RequestClearPersonalInfo } from "../../State/personalInfo";
import { RequestClear as RequestClearPassportInfo } from "../../State/passportInfo";
import { RequestClear as RequestClearattachmentDocuments } from "../../State/attachmentDocuments";
import {AppTypes_Clear} from '../../State/lookUps/types';

const CannotAccessThisSystem = () => {
	let userAuth = getLocalStorage("user", authenticateResponse);
	const history = useHistory();
	let dispatch = useDispatch();
	const loginstate = useSelector<RootState, RootState["login"]>(state => state.login);

	const [username, setUsername] = useState<string>("");
	const [password, setPassword] = useState<string>("");

	const [isFieldEmpty, setIsFieldEmpty] = useState(false);
	const [wrongCredintials, setWrongCredintials] = useState(false);
	let params: any = useParams();
	let location = useLocation();


	useEffect(() => {

		document.body.classList.add("sb-sidenav-toggled");
		return () => {
			document.body.classList.remove("sb-sidenav-toggled");
		}
	}, []);



	const redirect = () => {
	if(userAuth.response.userInfo.userRoleId == 1 || userAuth.response.userInfo.userRoleId == 3)
	{
		history.push("/Public/login");
	}
	else
	{
		localStorage.removeItem("user");
		dispatch(getllogout());
		dispatch(ClearRequest());
		dispatch(RequestClearPersonalInfo());
		dispatch(RequestClearPassportInfo());
		dispatch(RequestClearattachmentDocuments());
		dispatch({type:AppTypes_Clear});
		history.push("/Public/login");
	}

	}
	return (
		<Layout>

			
<main className="login-bg">
					<div className="container" style={{ marginBottom: '80px' }}>
						{/* Outer Row */}
						<div className="row justify-content-center">
							<div className="col-xl-10 col-lg-12 col-md-9">
								<div className="card o-hidden border-1 shadow my-5 animate__animated animate__backInDown " style={{ border: '1px solid rgb(189, 189, 189)' }}>
									<div className="card-header bg-dark">
										<div className="text-center">

											<h2 className=" text-gray-900 mb-1 shorooq gold" style={{ fontSize: '28px' }}>  </h2>
											<h1 className=" text-gray-900 mb-1 shorooq gold" style={{ fontSize: '28px' ,color:"red !important; " }}>          </h1>
										</div>
									</div>
									<div className="card-body p-0 ">
										{/* Nested Row within Card Body */}
										<div className="row">
											<div className="col-lg-8 p-5 text-center">
											<button className="btn btn-primary btn-user  shorooq " onClick={()=>redirect()} style={{ fontSize: '22px' ,padding:"inherit"}}>     </button>
										</div>
											<div className="col-lg-4 d-none d-lg-block text-center my-auto" style={{ backgroundColor: 'transparent' }}>
												<img className="img-fluid" src={process.env.PUBLIC_URL + 'img/logo.jpg'} alt="" width="80%" />
											</div>
										
											
										</div>
									</div>
								</div>
							</div>
						</div>
					</div>
				</main>
		

		

		
		</Layout>
	);
};

export default CannotAccessThisSystem;