import { paciServiceInput, paciServiceObj } from "../../Services/paciService"
import * as types from "./types"



export const setPaciClear = (): types.paciClearActionType => {
    return { type: types.PACI_CLEAR }

}
export const getPaciRequest = (input:paciServiceInput): types.paciRequestActionType => {
    return { type: types.PACI_REQUEST, payload: input}

}

export const getPaciSuccess = (input:paciServiceObj): types.paciSuccessActionType => {
    return { type: types.PACI_SUCCESS, payload: input }
}