import { UpdateApplicationAssignTo,GetV_CountAssignedApplications } from "./Urls"
import { IResponse } from "../types/response"
import { Ilookup } from "../types/lookUpTypes";
interface IUser {
    userName?: string;
    employeeName?: string;
    email?: string;
    civilId?: string;
    userRoleId?: number;
    employeeNumber?: string;
    userId?: number;
    userRoleName?: string;
}

export interface ICountApplicationsAndUsers
{
    Employee?:string;
    NotAssigned?:string;
}


export const UpdateApplicationAssignToFun = async (ApplicationNumber:any,UserID:any): Promise<any> => {

    let response = await fetch(UpdateApplicationAssignTo +ApplicationNumber  + "/"+UserID, {
        method: "PUT",
        headers: {'Content-Type': 'application/json'}
    });
    let res = await response.json();
    return res;
}



export const GetVCountAssignedApplications = async (): Promise<IResponse<ICountApplicationsAndUsers[]>> => {

    let response = await fetch(GetV_CountAssignedApplications, {
        method: "Get",
        headers: { 'Content-Type': 'application/json' },
    });
    let res:IResponse<ICountApplicationsAndUsers[]> = await response.json();
    return res;
}









