import React from 'react';
import { useSelector } from 'react-redux';
import { Redirect } from 'react-router-dom';
import { RootState } from '../../State/rootReducer';
import * as  Pages from '../index'

const Redirection = () => {
	let redirectionUrl = window.location.search.toLowerCase().replace("?", "");
    const authenticationState= useSelector<RootState,RootState["login"]>(state=>state.login);
    const {userInfo} =authenticationState;
  return <>
  
  {(userInfo != undefined &&(authenticationState.isLoggedIn && (userInfo.userRoleId==2 )&&(userInfo.userTypeId!=26)&&(userInfo.nationalityId?.toLowerCase()!="kw") && window.location.search == "") )&&
				<Redirect
					to={{
						pathname: "/newApp",
					}}
				/>
				}
  {(userInfo != undefined &&(authenticationState.isLoggedIn && (userInfo.userRoleId==1||userInfo.userRoleId==3) && window.location.search == "")) &&
				<Redirect
					to={{
						pathname: "/Res/CommonPages/LandingPage",
					}}
				/>
			}
		
			{/* {alert("../.."+redirectionUrl)} */}
			{(authenticationState.isLoggedIn && window.location.search != "") &&
				<Redirect
					to={{
						pathname: redirectionUrl,
					}}
				/>
			}
            </>;
};

export default Redirection;