import React from "react";
import { Redirect, Route } from "react-router-dom";

import { getLocalStorage } from "../../../Services/utils/localStorageHelper"

import { authenticateResponse } from "../../../types/userInfo";
import LayoutAdminSettings from "../../../Layout/ResidencySec/Manager/LayoutAdminSettings";
import { useSelector } from "react-redux";
import { RootState } from "../../../State/rootReducer";

const ProtectedRouteAdminSettings: React.ComponentType<any> = ({ component: Component, ...rest }) => {
	
	const authenticationState= useSelector<RootState,RootState["login"]>(state=>state.login);

	return (
		<Route
			{...rest}
			render={(props) => {
				if (authenticationState.isLoggedIn===true) {
					if (( (authenticationState.userInfo?.userRoleId == 1))) 
					{
						return <LayoutAdminSettings><Component {...props} /></LayoutAdminSettings>;
					}else
					{
						//you have permistion to access this pages 
						return (<Redirect
							to={{
								pathname: "/CannotAccessThisPages",
								state: { from: props.location },
							}}
						/>); 

					}
				
				
				}
				else {
					return (<Redirect
						to={{
							pathname: "/Public/login",
							state: { from: props.location },
						}}
					/>);
				}
			}}
		/>
	);
};

export default ProtectedRouteAdminSettings;