import React, { ReactElement, useState, useEffect, useRef } from 'react'
import { useForm, Controller } from "react-hook-form";
import { toast } from 'react-toastify';
import { useSelector, useDispatch } from "react-redux";
import { Steps, ErrorMessages } from "../../../types/Enums"
import { RootState } from "../../../State/rootReducer";
import { userInfo } from '../../../types/userInfo';
import { getGeneralSettings, updateGeneralSettings } from "../../../Services/GeneralSettings"
import Switch from "react-switch";

export default function GeneralSettings(): ReactElement {
    const loginData = useSelector<RootState, RootState["login"]>(state => state.login);
    const [generalSettings, setGenralSettings] = useState<any>(undefined);

    const { register, handleSubmit, watch, errors, control ,setValue} = useForm<any>();
    const onSubmit = async (data: any) => {
        let response = await updateGeneralSettings(data);
        if (!response.hasError) {
            if (response.message != "")
                toast.success(response.message);
        }
        else {
            toast.error(response.message);
        }

    }

    useEffect(() => {
        const loadData = async () => {
            let response = await getGeneralSettings();
            setGenralSettings(response?.response);
             setValue("FeatureId1",generalSettings?.featureId1);
             setValue("FeatureId2",generalSettings?.featureId2);
             setValue("FeatureId3",generalSettings?.featureId3);
        }
        loadData();
    }, []);

    useEffect(() => {
             setValue("FeatureId1",generalSettings?.featureId1);
             setValue("FeatureId2",generalSettings?.featureId2);
             setValue("FeatureId3",generalSettings?.featureId3);
    }, [generalSettings]);
    
    return (
        <main className="login-bg">

            <div className="container" style={{ marginBottom: 80 }}>
                {/* Outer Row */}
                <div className="row justify-content-center">
                    <div className="col-xl-12 col-lg-12 col-md-12">
                        <div className="card o-hidden border-1 shadow my-5 animate__animated animate__backInDown "
                            style={{ border: "1px solid rgb(189, 189, 189)" }}>
                            <div className="card-header bg-dark">
                                <div className="text-center">
                                    <h1
                                        className=" text-gray-900 mb-1 shorooq gold"
                                        style={{ fontSize: 28 }}
                                    >
                                         
                                    </h1>
                                </div>
                            </div>
                            <div className="card-body p-0 ">
                                {/* Nested Row within Card Body */}
                                <div className="row">
                                    <div className="col-lg-12">
                                        <div className="p-5">
                                            <form className="user" onSubmit={handleSubmit(onSubmit)}>
                                                {/* ################### form- row-002 #################*/}
                                                <div className="form-group row">
                                                    <div className="col-sm-1">

                                                        <input
                                                            name="FeatureId1"
                                                            type="text"
                                                           
                                                            className="form-control form-control-user"
                                                            ref={register({required:true})}
                                                        />
                                                    </div>
                                                    <label className="col-sm-5 col-form-label">
                                                              
                                                    </label>
                                                </div>
                                                {/* ################### form- row-003 #################*/}
                                                <div className="form-group row">
                                                    <label className="col-sm-3 col-form-label">
                                                           
                                                    </label>
                                                    <div className="col-2 custom-control custom-checkbox  mt-1">
                                                        <div >
                                                            <Controller
                                                                name="FeatureId2"
                                                                control={control}
                                                                defaultValue={false}
                                                                render={({ onChange, value }) => (
                                                                    <Switch
                                                                        onChange={onChange}
                                                                        checked={value}
                                                                    />
                                                                )}
                                                            />
                                                        </div>
                                                    </div>
                                                    <label className="col-sm-3 col-form-label">
                                                               
                                                    </label>
                                                    <div className="col-2 custom-control custom-checkbox  mt-1">
                                                        <Controller
                                                            name="FeatureId3"
                                                            control={control}
                                                            defaultValue={false}
                                                            render={({ onChange, value }) => (
                                                                <Switch
                                                                    onChange={onChange}
                                                                    checked={value}
                                                                    disabled
                                                                />
                                                            )}
                                                        /></div>
                                                </div>
                                                {/* ################### form- row-006 #################*/}
                                                {/* ################# submit btn ##################### */}
                                                <div className="row justify-content-end ml-2">
                                                    {/* <button type="submit"></button> */}
                                                    <button type="submit" className="btn btn-primary btn-user shorooq registeralert " style={{ fontSize: '22px' }}>
                                                        
                                                    </button>
                                                </div>

                                                {/* ################# end submit btn ##################### */}
                                            </form>
                                        </div>
                                    </div>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
            </div>

        </main>

    )
}
