import * as React from 'react';
import { TableBootStrap, columnsBootStrap } from "../../../Controls/Datatables"
import Layout from "../../../Layout/ClientSec/Layout";
import NoData from "../../Common/NoData";
import { useSelector, useDispatch } from "react-redux";
import { RootState } from "../../../State/rootReducer";
import { getUserTypesRequest } from "../../../State/lookUps";
import { useForm, useController, Controller } from "react-hook-form"
import { Steps, ErrorMessages } from "../../../types/Enums"
import { getUpdateUserInfo,updateUpdateUserInfo } from "../../../Services/UpdateUserInfo"
import { toast } from 'react-toastify';
import { useHistory, useLocation } from "react-router-dom";
import { ReactDatePicker as CustomReactDatePicker } from "../../../Controls/CustomDatePicker";
import Select from 'react-select';
import Switch from "react-switch";
import { getNationalities, getUserTypes, getOrganizationTypes } from "../../../Services/lookup";
import { SelectOptions } from "../../../types/UIRelated";
import { assignToSelectType, assignToType } from "../../../Services/utils/assignType";
import { useEffect } from 'react';

interface iUserInfo {
  civilId: string;
  organization: string;
  usertypeid: string;
  phone: string;
  email: string;

}

class UserInfoInputObj {
  userid: number = 0;
  email: string = "";
  usertypeid: number = 0;
  organization: number = 0;
  phone: string = "";

}
const UpdateUserInfo = () => {
  //#region Intiate Objects
  const LookUpState = useSelector<RootState, RootState["lookUp"]>(state => state.lookUp);
  const { UserTypes } = LookUpState;
  let dispatch = useDispatch();
  const history = useHistory();
  const location = useLocation();
  const [selectedUserId, setSelectedUserId] = React.useState<number>(0);
  const [visible, setVisile] = React.useState(true);
  const [userTypes, setuserTypesOption] = React.useState<SelectOptions[]>();
  const [organization, setOrganization] = React.useState<SelectOptions[]>();
  const [userInfo, setUserInfo] = React.useState<iUserInfo>({ civilId: "", email: "", organization: "", phone: "", usertypeid: "" });
  const [buttonType, setbuttonType] = React.useState<string>("");

  let row: any = location.state;

  const { reset, handleSubmit, errors, register, setValue, control } = useForm<any>({});
  const { reset: reset1, handleSubmit: handleSubmit1, errors: errors1, register: register1, setValue: setValue1, control: control1 } = useForm<any>({});
  //#endregion
  //#region Events
  React.useEffect(() => {
    const GetDropdownValues = async () => {
      if (UserTypes === undefined) {
        dispatch(getUserTypesRequest());
      }
      let responseOrganization = await getOrganizationTypes();
      let organizationOptions: SelectOptions[] = assignToSelectType(responseOrganization, "value", "label");
      setOrganization(organizationOptions);
      let responseUserTypes = await getUserTypes();
      let userTypesOptions: SelectOptions[] = assignToSelectType(responseUserTypes, "value", "label");
      setuserTypesOption(userTypesOptions);
    };
    GetDropdownValues();
    setVisile(true);
    return () => {
     
    };
  }, []);
  const submit = async (data: any) => {
    setVisile(true);
    let response;
    let input = { 'civilId': data?.civilId, 'name': data?.name }
    response = await getUpdateUserInfo(input);
    setUserInfo(response.response);
    setSelectedUserId(response.response?.userId);
    if (!response.hasError) {
      setVisile(false);
      if (response.message != "")
        toast.success(response.message);
    }
    else {
      setVisile(true);

      toast.error(response.message);
    }
  }

  const submit1 = async (data: any) => {
    let input: UserInfoInputObj = new UserInfoInputObj();
    input.userid = selectedUserId;
    input.organization = data.selectorganization?.value as number ;
    input.usertypeid = data.userTypeId.value as number ;
    input.email = data.email;
    input.phone=data.mobileNo;
    let response = await updateUpdateUserInfo(input);
    if (!response.hasError) {
      if (response.message != "")
        toast.success(response.message);
    }
    else {
      toast.error(response.message);
    }
  
    
  }

  React.useEffect(() => {
    setValue1("email", userInfo?.email);
    setValue1("mobileNo", userInfo?.phone);

    if (organization !== undefined ) {
      setValue1("selectorganization", (organization as SelectOptions[])?.find(j => j.value === ''+userInfo?.organization));
    }
    if (userTypes !== undefined) {
      setValue1("userTypeId", (userTypes as SelectOptions[])?.find(j => j.value === '' + userInfo?.usertypeid));

    }
  }, [userInfo, organization, userTypes]);

  const resetForm = () => {
    reset();
    history.replace('/settingsUpdateUserInfo', undefined);
    row = undefined;
    location.state = undefined;
    setbuttonType("");
  }

  React.useEffect(() => {
    row = location.state;
    setValue("userTypeName", row?.label);
  }, [location]);

  //#endregion


  type handleRowFunctions = React.ElementRef<typeof TableBootStrap>;
  const handleRowFunctionsref = React.useRef<handleRowFunctions>(null);
  let columnsBootStrap_: columnsBootStrap = { columns: [], data: null, tableName: "", hasSelection: false };
  columnsBootStrap_.columns = [
    {
      text: " ",
      dataField: "value"
    },
    {
      text: "",
      dataField: "label"
    }
    ,
    {
      text: "",
      formatter: (row: any, cell: any) => <button className="btn btn-warning btn-sm  rounded-circle" onClick={() => handleRowFunctionsref.current?.EditUserTypesRow(cell)} ><i className="fas fa-edit" aria-hidden="true" /></button>
    }
  ];
  return <fieldset className="shadow-sm">
    <main className="login-bg">
      <div className="container" style={{ marginBottom: 80 }}>
        {/* Outer Row */}
        <div className="row justify-content-center">
          <div className="col-xl-12 col-lg-12 col-md-12">
            <div className="card o-hidden border-1 shadow my-5 animate__animated animate__backInDown " style={{ border: '1px solid rgb(189, 189, 189)' }}>
              <div className="card-header bg-dark">
                <div className="text-center">
                  <h1 className=" text-gray-900 mb-1 shorooq gold" style={{ fontSize: 28 }}>   - 
                     </h1>
                </div>
              </div>
              <div className="card-body p-0 ">
                {/* Nested Row within Card Body */}
                <div className="row">
                  <div className="col-lg-12">
                    <div className="p-5">
                      <form className="user" onSubmit={handleSubmit(submit)} >
                        {/* ################### form- row-002 #################*/}
                        <div className="form-group row">
                          <label className="col-sm-3 col-form-label"> </label>
                          <div className="col-sm-3">
                            <input name="civilId" type="text" className="form-control form-control-user" ref={register()} />
                          </div>
                          <label className="col-sm-3 col-form-label"> </label>
                          <div className="col-sm-3">
                            <input name="name" type="text" className="form-control form-control-user" ref={register()} />
                          </div>
                        </div>
                        {/* ################### form- row-003 #################*/}
                        <div className="form-group row">
                          {/* <label className="col-sm-3 col-form-label">   </label>
                          <div className="col-sm-3">
                            <Controller
                              control={control}
                              name="fromDate"
                              className="form-control"
                              render={({ onChange, onBlur, value }) => (
                                <CustomReactDatePicker required={false} onChange={onChange} onBlur={onBlur} ss={value} />
                              )}
                            />
                          </div> */}
                          {/* <label className="col-sm-3 col-form-label">  </label>
                          <div className="col-sm-3">
                            <Controller
                              control={control}
                              name="toDate"
                              className="form-control"
                              render={({ onChange, onBlur, value }) => (
                                <CustomReactDatePicker required={false} onChange={onChange} onBlur={onBlur} ss={value} />
                              )}
                            />
                          </div> */}
                        </div>
                        {/* ################### form- row-006 #################*/}
                        {/* ################# submit btn ##################### */}
                        <div className="row justify-content-end ml-2">
                          <button className="btn btn-primary btn-user shorooq  " style={{ fontSize: 22 }}>  </button> </div>
                        {/* ################# end submit btn ##################### */}
                      </form>
                    </div>
                  </div>
                </div>
                <hr />
                <div className="row">
                  <div className="col-lg-12">
                    <div className="p-5" style={{ display: visible==true?"none":"block" }}   >
                      <form className="user" onSubmit={handleSubmit1(submit1)} >
                        {/* ################### form- row-002 #################*/}
                        <div className="form-group row">
                          <label className="col-sm-3 col-form-label">  </label>
                          <div className="col-sm-3">
                            <input name="mobileNo" type="text" className="form-control form-control-user" ref={register1()} />
                          </div>
                          <label className="col-sm-3 col-form-label"> </label>
                          <div className="col-sm-3">
                            <input name="email" type="text" className="form-control form-control-user" ref={register1()} />
                          </div>
                        </div>
                        {/* ################### form- row-003 #################*/}
                        <div className="form-group row">
                          <label className="col-sm-3 col-form-label">/</label>
                          <div className="col-sm-3">
                            <Controller
                              name="selectorganization"
                              control={control1}
                              placeholder="   "
                              className="form-control form-control-user border-0"
                              options={organization}

                              as={Select}

                            />
                          </div>
                          <label className="col-sm-3 col-form-label">  </label>
                          <div className="col-sm-3">
                            <Controller
                              name="userTypeId"
                              control={control1}
                              className="form-control form-control-user border-0"
                              placeholder="   "
                              options={userTypes}
                              ref={register1()}
                              as={Select}
                            />
                          </div>
                        </div>
                        {/* ################### form- row-001 #################*/}
                        <div className="form-group row">
                          <label className="col-sm-3 col-form-label">   </label>
                          <div className="col-3 custom-control custom-checkbox  mt-1">
                            <Controller
                              name="isMOA"
                              control={control1}
                              defaultValue={true}
                              disabled
                              render={({ onChange, value }) => (
                                <Switch
                                  onChange={onChange}
                                  checked={value}
                                  disabled
                                />
                              )}
                            />
                          </div>
                        </div>
                        {/* ################### form- row-006 #################*/}
                        {/* ################# submit btn ##################### */}
                        <button className="btn btn-primary btn-user shorooq  " style={{ fontSize: 22 }}>  </button>

                        {/* ################# end submit btn ##################### */}
                      </form>
                    </div>
                    <div className="p-5" style={{ display: visible==true?"block":"none", height:"400px" }}   >
                      {/* <NoData Message="    "></NoData> */}
                      </div>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    </main>
  </fieldset>
    ;
};

export default UpdateUserInfo;


