import React from 'react';
import RouteClientSec from './ProtectedRoute';
import * as Pages from './../../Pages/index'
import { Route } from 'react-router-dom';

const Index = () => {
  return <Route>
  <RouteClientSec exact path="/Client/" component={Pages.ClientSecPages.NewApp} />
  <RouteClientSec exact path="/Client/NewApp" component={Pages.ClientSecPages.NewApp} />
  <RouteClientSec exact path="/Client/PersonalInfo" component={Pages.ClientSecPages.PersonalInfo} />
  <RouteClientSec exact path="/Client/PassportInfo" component={Pages.ClientSecPages.PassportInfo} />
  <RouteClientSec exact path="/Client/FileAttachments" component={Pages.ClientSecPages.FileAttachments} />
  <RouteClientSec exact path="/Client/Agreament" component={Pages.ClientSecPages.Agreament} />
  <RouteClientSec exact path="/Client/ManageRequests" component={Pages.ClientSecPages.ManageRequests} />
  <RouteClientSec exact path="/Client/ChangePassword" component={Pages.ClientSecPages.ChangePassword} />
  <RouteClientSec exact path="/Client/Profile" component={Pages.ClientSecPages.Profile} />
  <RouteClientSec exact path="/Client/Result" component={Pages.ClientSecPages.Result} />
  <RouteClientSec exact path="/Client/OnlyView/NewApp" component={Pages.ClientSecPages.OnlyView.NewApp} />
  <RouteClientSec exact path="/Client/OnlyView/PersonalInfo" component={Pages.ClientSecPages.OnlyView.PersonalInfo} />
  <RouteClientSec exact path="/Client/OnlyView/PassportInfo" component={Pages.ClientSecPages.OnlyView.PassportInfo} />
  <RouteClientSec exact path="/Client/OnlyView/FileAttachments" component={Pages.ClientSecPages.OnlyView.FileAttachments} />
</Route>;
};

export default Index;
