import * as Types from './types';


const initialState: Types.IStateApp = {
  IState: {
    applicationDate: undefined,
    applicationStatusId: undefined,
    applicationNumber: undefined,
    applicationTypeId: undefined,
    isActive: undefined,
    remark: undefined,
    stepNo: undefined,
    userId: undefined
  },
  isloading: false,
  hasError: false,
  message: ""
}


export function newAppReducer(state: Types.IStateApp = initialState,
  action: Types.NewAppActionsTypes): Types.IStateApp {


    switch (action.type) {

    case Types.Clear:
      localStorage.setItem("newApp", JSON.stringify({ ...initialState }));

      return {
        ...initialState
      }
    case Types.Loading:
      localStorage.setItem("newApp", JSON.stringify({ ...state, isloading: action.payload }));

      return {
        ...state,
        isloading: action.payload

      }
    case Types.CreateSuccess:
      localStorage.setItem("newApp", JSON.stringify({ ...state, IState: action.payload.IState, isloading: false, hasError: action.payload.hasError, message: action.payload.message }));
      return {
        ...state,
        IState: action.payload.IState,
        isloading: false,
        hasError: action.payload.hasError,
        message: action.payload.message

      }
    case Types.IncompleteFetchSuccess:
      localStorage.setItem("newApp", JSON.stringify({ ...state, IState: action.payload.IState, isloading: false, hasError: action.payload.hasError, message: action.payload.message }));

      return {
        IState: action.payload.IState,
        isloading: false,
        hasError: action.payload.hasError,
        message: action.payload.message
      }
    case Types.UpdateSuccess:
      localStorage.setItem("newApp", JSON.stringify({ ...state, IState: action.payload.IState, isloading: false, hasError: action.payload.hasError, message: action.payload.message }));
      
      return {
       
        IState: action.payload.IState,
        isloading: false,
        hasError: action.payload.hasError,
        message: action.payload.message
      }
    default:
      let localStorageData: any = localStorage.getItem('newApp');
      if (localStorageData) {
        localStorageData = JSON.parse(localStorageData);
        return {
          IState: localStorageData.IState,
          isloading: localStorageData.isloading,
          hasError: localStorageData.hasError,
          message: localStorageData.message

        }
      }
      return state;
      return state
  }

}

export default newAppReducer;