import axios from 'axios';

const BaseUrl = '';
const AuthUrl = '';

//https://localhost:44367/api/UserApplications/UserId/5
axios.interceptors.request.use(function (config) {
    let localStorageData :any= localStorage.getItem('user');
    if (localStorageData) {
        localStorageData = JSON.parse(localStorageData);
        let token = 'Bearer ' + localStorageData.response.jwtToken ;
        config.headers.Authorization = token;
    }
    return config;
},(error) => {
    return Promise.reject(error);
  });
axios.interceptors.response.use(function (response) {
    return response.data;
}, function (error) {
    if (error.response.status === 401) {
        localStorage.removeItem('data');
        window.location.href = '/Public/login';
    } else {       
        return Promise.reject(error.response);
    }
});
export const GetUserApplicationsByUserId = (input:any) => {
    return axios.get<any>(`/api/UserApplications/UserId/`+input);
}
export const GetUserApplicationsAdmin = (userId:any) => {
    return axios.get<any>(`/api/UserApplications/List?Type=inward&userid=`+userId);
}
export const GetUserApplicationsAdminOutward = (userId:any) => {
    return axios.get<any>(`/api/UserApplications/List?Type=outward&userid=`+userId);
}
export const GetUserApplicationsAdminInwardSearch = (data:any) => {
    return axios.get<any>(`/api/UserApplications/SearchList?ApplicationNumber=${data.ApplicationNumber}&ApplicationTypeId=${data.ApplicationTypeId}&UserTypeId=${data.UserTypeId}&CivilId=${data.CivilId}&ApplicationStatusId=${data.ApplicationStatusId}&EmployeeName=${data.EmployeeName}&SubmittedBy=${data.submittedBy}&EmployeeNumber=${data.EmployeeNumber}&AssignedTo=${data.userId}&FromDate=${data.FromDate}&ToDate=${data.ToDate}`);
}
export const DeleteUserApplicationById = (id:any) => {
    return axios.delete(`${BaseUrl}/posts/${id}`, id);
}
export const createPost = (data:any) => {
    return axios.post(`${BaseUrl}/posts`, data);
}
export const editPost = (data:any) => {
    return axios.put(`${BaseUrl}/posts`, data);
}
export const deletePost = (id:any) => {
    return axios.delete(`${BaseUrl}/posts/${id}`, id);
}