import {
  LoginActionTypes,
  AuthState,
  LOGIN_SUCCESS,
  LOGIN_LOADING,
  LOGOUT,
  PROFILECHANGE,
  USERUPDATEINFOSUCCESS,
  SELECTUSER,
  SELECTUSERSUCCESS,
  CLEARSELECTEDUSER
} from './types'
import { UserInfo } from "../../types/userInfo";
import { userInfo } from 'os';
import { selectUser } from '.';
const initialState: AuthState = {
  isLoggedIn: false,
  jwtToken: undefined,
  userInfo: undefined,
  isLoading: true,
  selectedUser:undefined
}

export function loginReducer(state: AuthState = initialState, action: LoginActionTypes): AuthState {

  switch (action.type) {
    case SELECTUSERSUCCESS:
      return {
        ...state,
        selectedUser:action.payload
      }
      case CLEARSELECTEDUSER:
      return {
        ...state,
        selectedUser:undefined
      }
    case LOGIN_LOADING:
      return {
        ...state,
        isLoading: true
      }
      case USERUPDATEINFOSUCCESS :
        let temp_ = new UserInfo();
        temp_ = state.userInfo as UserInfo;
        temp_.mobileNumber = action.payload.mobileNumber;
        temp_.civilIdSerialNumber = action.payload.civilIdSerial;
        temp_.employeeNumber = action.payload.employeeNumber;
        temp_.sectionId = action.payload.sectionId;
        temp_.userTypeId = action.payload.userType;
        temp_.jobtypeId = action.payload.jobTypeId;

        let localData_: any = localStorage.getItem('user');
        if (localData_) {
          localData_ = JSON.parse(localData_);
          localData_.response.userInfo = temp_;
        }
        localStorage.setItem("user", JSON.stringify(localData_));
        return {
          ...state,
          userInfo: temp_
        }
    case LOGIN_SUCCESS:
      return {
        ...state,
        isLoggedIn: action.payload.isLoggedIn,
        isLoading: false,
        userInfo: action.payload.userInfo,
        jwtToken: action.payload.jwtToken,
        hasError: action.payload.hasError,
        message: action.payload.message,
        

      }
    case LOGOUT:
      return {
        ...state,
        isLoggedIn: false,
        isLoading: false,
        jwtToken: undefined,
        userInfo: undefined,
        hasError: false,
        message: ""
      }
    case PROFILECHANGE:
      let temp = new UserInfo();
      temp = state.userInfo as UserInfo;
      temp.email = action.payload.email;
      temp.mobileNumber = action.payload.mobileNumber;
      temp.civilIdSerialNumber = action.payload.civilIdSerialNumber;
      let localData: any = localStorage.getItem('user');
      if (localData) {
        localData = JSON.parse(localData);
        localData.response.userInfo = temp;
      }
      localStorage.setItem("user", JSON.stringify(localData));
      return {
        ...state,
        userInfo: temp
      }


    default:
      let localStorageData: any = localStorage.getItem('user');
      if (localStorageData) {
        localStorageData = JSON.parse(localStorageData);
        return {
          ...state,
          isLoggedIn: true,
          isLoading: false,
          jwtToken: localStorageData.response.jwtToken,
          userInfo: localStorageData.response.userInfo,
          hasError: localStorageData.hasError,
          message: localStorageData.message
        }
      }

      return state;

  }
}

export default loginReducer;