import * as React from 'react';
import { useSelector, useDispatch } from 'react-redux';
import { useParams } from 'react-router';
import { RootState } from "../../../State/rootReducer"
import { TableBootStrap, columnsBootStrap } from "../../../Controls/Datatables"
import { getNonSapUsersRegistered } from "../../../Services/nonSapUsers";
import { toast } from 'react-toastify';
import { useHistory } from "react-router-dom";
import { IResponse } from "../../../types";
import NoData from "../../Common/NoData";


const RegistrationActivationSettings = () => {

    //Table boot strap input {columns,handel pop up function its (ref) function}
    const [registeredUsers, setRegisteredUsers] = React.useState<any>();
    const history = useHistory();
    type handleRowFunctions = React.ElementRef<typeof TableBootStrap>;
    const handleRowFunctionsref = React.useRef<handleRowFunctions>(null);
    let columnsBootStrap_: columnsBootStrap = { columns: [], data: null, tableName: "", hasSelection: false };
    columnsBootStrap_.columns = [
      
        {
            text: " ",
            dataField: "civilId"
        },
        {
            text: " ",
            dataField: "registrationDate",
            formatter: (row: any, cell: any) => <span> {new Date(cell.registrationDate).toLocaleDateString('ar-EG-u-nu-latn', { weekday: 'long', year: 'numeric', month: 'short', day: 'numeric' })}   </span>

        },
        {
            text: " ",
            dataField: "employeeName"
        },
        {
            text: "",
            dataField: "organization"
        },
        {
            text: "   ",
            dataField: "userTypeName"
        }
        ,
        {
            text:  "",
            dataField: "jobName"
        }
        ,
        {
            text:  "",
            dataField: "registrationStatusNameAr",
            formatter:(row:any,cell:any)=> <td className="h-100 w-100 d-inline-block"  style={{background:cell?.registrationStatusId==2?"#28a745":cell?.registrationStatusId==3?"#dc3545":cell?.registrationStatusId==3?"#ffc107":"#0260ae"}}>{cell.registrationStatusNameAr}</td>

        }
        // ,
        // {
        //     text: "",
        //     formatter: (row: any, cell: any) => <button className="btn btn-primary btn-sm  rounded-circle" onClick={() => handleRowFunctionsref.current?.EditRowAdmin(cell)} ><i className="fas fa-eye" aria-hidden="true" /></button>
        // }
        ,
        {
            text: "",
            formatter: (row: any, cell: any) => <button className="btn btn-success btn-sm  rounded-circle" onClick={() => handleRowFunctionsref.current?.acceptUsers(cell)} ><i className="fa fa-check" aria-hidden="true" /></button>
        },
        {
            text: "",
            formatter: (row: any, cell: any) => <button className="btn btn-danger btn-sm  rounded-circle" onClick={() => handleRowFunctionsref.current?.rejectUsers(cell)} > <i className="fa fa-ban" aria-hidden="true"></i></button>
        }
       
    ];
    const loadData = async () => {
        let result: any = await getNonSapUsersRegistered();
        if (!result.hasError) {
            if(result.message!="")
            toast.success(result.message);
        }
        else {
            toast.error(result.message);
        }
        setRegisteredUsers(result.response.map((user:any) => user));
    }
    React.useEffect(() => {
        loadData();
    },[]);



    return <>
    {(registeredUsers!=null || registeredUsers!=undefined  )&& <TableBootStrap  hasSelection={false}  ref={handleRowFunctionsref}  data={registeredUsers} columns={columnsBootStrap_.columns}  tableName="  " />}
    {(registeredUsers==null || registeredUsers==undefined )&& <NoData Message="     "/>}

    </>;
};

export default RegistrationActivationSettings;
