import { LDABURL, ADMINURL, USER_ROLE_LOOKUP, putUserRoleIdURL, addUserToDbWithUserRoleIdURL } from "./Urls"
import { IResponse } from "../types/response"
import { Ilookup } from "../types/lookUpTypes";
interface IUser {
    userName?: string;
    employeeName?: string;
    email?: string;
    civilId?: string;
    userRoleId?: number;
    employeeNumber?: string;
    userId?: number;
    userRoleName?: string;
}

export const getLdabUsersList = async (input: string): Promise<IResponse<IUser[]>> => {

    let response = await fetch(LDABURL + "?KEY=" + input, {
        method: "Get",
        headers: { 'Content-Type': 'application/json' },
    });
    let res: IResponse<IUser[]> = await response.json();
    return res;
}

export const getAdminUsers = async (): Promise<IResponse<IUser[]>> => {

    let response = await fetch(ADMINURL, {
        method: "Get",
        headers: { 'Content-Type': 'application/json' },
    });
    let res: IResponse<IUser[]> = await response.json();

    return res;
}


export const getAdminUsersByLevel = async (level:any): Promise<IResponse<IUser[]>> => {

    let response = await fetch(ADMINURL+"/"+level, {
        method: "Get",
        headers: { 'Content-Type': 'application/json' },
    });
    let res: IResponse<IUser[]> = await response.json();

    return res;
}

export const getUserRoleLookup = async (): Promise<Ilookup[]> => {

    let response = await fetch(USER_ROLE_LOOKUP, {
        method: "Get",
        headers: { 'Content-Type': 'application/json' },
    });
    let res: Ilookup[] = await response.json();
    return res;
}

export const putUserRoleIdCall = async (input:any): Promise<any> => {

    let response = await fetch(putUserRoleIdURL + "/"+input?.userId + "/"+input.userRoleId, {
        method: "PUT",
        headers: { 'Content-Type': 'application/json' }
    });
    let res = await response.json();
    return res;
}



export const addUserToDbWithUserRoleIdCall = async (input:any): Promise<any> => {

    let response = await fetch(addUserToDbWithUserRoleIdURL, {
        method: "POST",
        headers: { 'Content-Type': 'application/json' },
        body: JSON.stringify({ ...input })
    });
    let res = await response.json();
    return res;
}


