import * as React from 'react';
import { TableBootStrap, columnsBootStrap } from "../../../Controls/Datatables"
import Layout from "../../../Layout/ClientSec/Layout";
import NoData from "../../Common/NoData";
import { useSelector, useDispatch } from "react-redux";
import { RootState } from "../../../State/rootReducer";
import { getUserTypesRequest } from "../../../State/lookUps";
import { useForm, useController } from "react-hook-form"
import { Steps, ErrorMessages } from "../../../types/Enums"
import { deleteUserTypesSettings, insertUserTypesSettings, updateUserTypesSettings } from "../../../Services/UserTypesSettings"
import { toast } from 'react-toastify';
import { useHistory, useLocation } from "react-router-dom";

interface IAppProps {

}

const UserTypesSettings = () => {
  //#region Intiate Objects
  const LookUpState = useSelector<RootState, RootState["lookUp"]>(state => state.lookUp);
  const { UserTypes } = LookUpState;
  let dispatch = useDispatch();
  
  const [buttonType, setbuttonType] = React.useState<string>("");
  const history = useHistory();
  const location = useLocation();
  let row: any = location.state;
  const { reset, handleSubmit, errors, register,setValue } = useForm<any>({
   
  });
  //#endregion
  //#region Events
  React.useEffect(() => {
    const GetDropdownValues = async () => {
      if (UserTypes === undefined) {
        dispatch(getUserTypesRequest());
      }
    };
    GetDropdownValues();
    
    return () => {
      row = undefined;
      location.state = undefined;
    //  history.replace('/settings/UserTypesSettings',undefined) ;
    }; 
  }, []);
  const submit = async (data: any) => {
    let responce ;
    if (buttonType == "save") {
      
      
      if (row == undefined) {
        let input = { 'value': '', 'label': data?.userTypeName }
         responce = await insertUserTypesSettings(input);
      } else {
        let input = { 'value': row?.value, 'label': data?.userTypeName }
        responce = await updateUserTypesSettings(input);
      }

      if (responce?.hasError) {
        toast.error(responce?.message);
      }
      else {
        toast.success(responce?.message);
        dispatch(getUserTypesRequest());
        resetForm();
      }

    }
    else if (buttonType == "delete"){
      let input = { 'input':Number( row?.value) }
      responce = await deleteUserTypesSettings(input);
      if (responce?.hasError) {
        toast.error(responce?.message);
      }
      else {
        toast.success(responce?.message);
        dispatch(getUserTypesRequest());
        resetForm();
      }
    }


  }
  const resetForm = () => {
    reset();
    history.replace('/settingsUserTypesSettings',undefined) ;
    row = undefined;
    location.state = undefined;
    setbuttonType("");
  }
  React.useEffect(() => {
    row = location.state;
    setValue("userTypeName", row?.label);
  }, [location]);
  //#endregion


  type handleRowFunctions = React.ElementRef<typeof TableBootStrap>;
  const handleRowFunctionsref = React.useRef<handleRowFunctions>(null);
  let columnsBootStrap_: columnsBootStrap = { columns: [], data: null, tableName: "", hasSelection: false };
  columnsBootStrap_.columns = [
    {
      text: " ",
      dataField: "value"
    },
    {
      text: "",
      dataField: "label"
    }
    ,
    {
      text: "",
      formatter: (row: any, cell: any) => <button className="btn btn-warning btn-sm  rounded-circle" onClick={() => handleRowFunctionsref.current?.EditUserTypesRow(cell)} ><i className="fas fa-edit" aria-hidden="true" /></button>
    }
  ];
  return <fieldset className="shadow-sm">
    <legend className="shadow-sm">
      <div className="text-center">    </div>
    </legend>
    {/* ######################################################### */}
    <div className="alert alert-secondary border  rounded rounded-lg justify-content-center text-center noor-Bold">  </div>
    <div className="row mb-5">
      <form onSubmit={handleSubmit(submit)}>
        <div className="row" style={{marginRight:'40px'}}>

          <div className="col-sm-3 text-center ">
            <div className="row">
              <label className="alert-secondary p-2 w-80 shadow-sm"> </label>
              <span className="mt-3" style={{ color: "#ff0000" }}>*</span>
            </div>
          </div>
          <div className="col">
            <div className="row">
              <input type="text" name="userTypeName" className="form-control" aria-describedby="helpId" ref={register({ required: true })} />

              {errors?.userTypeName?.type == "required" && <span className="text-danger">{ErrorMessages.required}</span>}
            </div>
          </div>
          <div className="col">
            <button onClick={() => setbuttonType("save")} className={row == undefined ? "btn btn-success btn-sm rounded-lg mr-2 " : "btn btn-warning btn-sm rounded-lg mr-2 "} style={{ minWidth: 120 }}><i className="fas fa-save   " /> &nbsp; {row == undefined ? "" : ""} </button>
           
          
          </div>
          <div className="col">
           
          {row!==undefined && <button onClick={() => setbuttonType("delete")} className={"btn btn-danger btn-sm rounded-lg mr-2 "} style={{ minWidth: 120 }}><i className="fas fa-save   " /> {""} </button>} 
        </div>
         
        </div>
      </form>
      <div className="col">
        <button onClick={() => resetForm()} className="btn btn-dark btn-sm rounded-lg mr-2 " style={{ minWidth: 120 }}><i className="fa fa-stop-circle" aria-hidden="true" /> &nbsp; </button>
      </div>
    </div>

    {(UserTypes != undefined) && <TableBootStrap hasSelection={false} ref={handleRowFunctionsref} data={UserTypes} columns={columnsBootStrap_.columns} tableName="" />}

  </fieldset>
    ;
};

export default UserTypesSettings;


