import * as React from 'react';
import { useSelector, useDispatch } from 'react-redux';
import { RootState } from "../../../State/rootReducer"
import { TableBootStrap, columnsBootStrap } from "../../../Controls/Datatables"
import Layout from "../../../Layout/ClientSec/Layout";
import NoData from "../../Common/NoData";
import { IRequests, IRequestsState } from '../../../State/ManageRequests/types';
import * as actions from "../../../State/ManageRequests/actions"
import * as Types from "../../../State/ManageRequests/types"
import { Alert } from 'reactstrap';
import { types } from 'util';
import { useForm, Controller } from "react-hook-form";
import Select from 'react-select';
import { IResponse } from '../../../types';
import { getAdminUsersByLevel } from '../../../Services/userPermissions'
import { AdminTypes } from "../../../types/Enums";
import { SelectOptions } from "../../../types/UIRelated";
import { assignToSelectType, assignToType } from "../../../Services/utils/assignType";
import {  toast } from 'react-toastify';
import { setLoading } from "../../../State/lookUps/action";
import {HorizontalBarChart} from "../../../Controls/Chart"
import {ICountApplicationsAndUsers,GetVCountAssignedApplications  } from "../../../Services/userApplication";
interface IUser {
  userName?: string;
  employeeName?: string;
  email?: string;
  civilId?: string;
  userRoleId?: number;
  employeeNumber?: string;
  userId?: number;
  userRoleName?: string;
  comeFromLdab?: boolean;
}

export default function InwardApplicationAssign() {

  const RequestsState: IRequestsState = useSelector<RootState, RootState["manageRequest"]>(state => state.manageRequest);
  const { userInfo } = useSelector<RootState, RootState["login"]>(state => state.login);
  const { featureId1, featureId2, featureId3 } = useSelector<RootState, RootState["generalSettings"]>(state => state.generalSettings);
  const { IRequests } = RequestsState;
  const { SelectedRequest } = RequestsState;
  const [alertVisible, setAlertVisible] = React.useState(false);
  const [alertType, setAlertType] = React.useState("");
  const [IRequests_, setIRequests_] = React.useState(RequestsState?.IRequests);
  let dispatch = useDispatch();
  const [exsistedUsers, setExsistedUsers] = React.useState<SelectOptions[]>([]);
   const [countOfAssigned,setCountOfAssigned]=React.useState<ICountApplicationsAndUsers[]>([]);
  //Table boot strap input {columns,handel pop up function its (ref) function}
  type handleRowFunctions = React.ElementRef<typeof TableBootStrap>;
  const handleRowFunctionsref = React.useRef<handleRowFunctions>(null);
  let columnsBootStrap_: columnsBootStrap = { columns: [], data: null, tableName: "", hasSelection: false };
  columnsBootStrap_.columns = [
    {
      text: " ",
      dataField: "applicationNumber"
    },
    {
      text: " ",
      dataField: "civilId"
    },
    {
      text: " ",
      dataField: "userName"
    },
    {
      text: " ",
      dataField: "applicationStatusName",
      formatter: (row: any, cell: any) => <td className="h-100 w-100 d-inline-block" style={{ background: cell?.applicationStatusId == 1 ? "#28a745" : cell?.applicationStatusId == 2 ? "#dc3545" : cell?.applicationStatusId == 3 ? "#ffc107" : "#0260ae" }}>{cell.applicationStatusName}</td>
    },
    {
      text: " ",
      dataField: "applicationTypeName"
    },
    {
      text: " ",
      dataField: "applicationDate",
      formatter: (row: any, cell: any) => <span> {new Date(cell.applicationDate).toLocaleDateString('ar-EG-u-nu-latn', { weekday: 'long', year: 'numeric', month: 'short', day: 'numeric' })}   </span>
    }
    //,
    // {
    //   text: "",
    //   formatter: (row: any, cell: any) => <button className="btn btn-primary btn-sm  rounded-circle" onClick={() => handleRowFunctionsref.current?.handlePopUp(cell)}><i className="fa fa-print" aria-hidden="true"></i></button>
    // }
    ,
    {
      text: "",
      formatter: (row: any, cell: any) => <button className="btn btn-primary btn-sm  rounded-circle" onClick={() => handleRowFunctionsref.current?.EditRowAdmin(cell)} ><i className="fas fa-eye" aria-hidden="true" /></button>
    }
    , 
    {
      text: " ",
      dataField: "assignTo"
    }
     //,
    // {
    //   text: "",
    //   formatter: (row: any, cell: any) => <button className="btn btn-success btn-sm  rounded-circle" onClick={() => handleRowFunctionsref.current?.SubmitRow(cell)} ><i className="fa fa-check" aria-hidden="true" /></button>
    // },
    // {
    //   text: "",
    //   formatter: (row: any, cell: any) => <button className="btn btn-danger btn-sm  rounded-circle" onClick={() => handleRowFunctionsref.current?.RejectRow(cell)} > <i className="fa fa-ban" aria-hidden="true"></i></button>
    // }
    // ,
    // {
    //   text: "",
    //   formatter: (row: any, cell: any) => <button disabled={featureId2 == true && false} className="btn btn-warning btn-sm  rounded-circle" onClick={() => handleRowFunctionsref.current?.ReturnRow(cell)} ><i className="fas fa-undo" aria-hidden="true" /></button>
    // },
    // {
    //   text: "",
    //   formatter: (row: any, cell: any) => <button className="btn btn-primary btn-sm  rounded-circle" onClick={() => handleRowFunctionsref.current?.handlepopUpComments(cell)}><i className="fa fa-comments" aria-hidden="true"></i></button>
    // }
  ];
  const { register, handleSubmit, watch, errors, control } = useForm<any>();
    //intial fetch for dropdown

  React.useEffect(() => {
    dispatch({ type: Types.Clear });
    dispatch(setLoading(true));
    const GetDropdownValues = async () => {

      let res: IResponse<IUser[]> = await getAdminUsersByLevel(AdminTypes.MidLevelAdmin);
      let getAdminUser: SelectOptions[] = assignToSelectType(res?.response as IUser[], "userId", "employeeName");
      let res_ :IResponse<ICountApplicationsAndUsers[]>=await GetVCountAssignedApplications();
      setCountOfAssigned(res_.response as ICountApplicationsAndUsers[]);
      setExsistedUsers(getAdminUser);
    };
    GetDropdownValues();
    dispatch(setLoading(false));

  }, []);
  React.useEffect(() => {
    dispatch(actions.CreateRequestAdmin(userInfo?.userId));

  }, [userInfo?.userId]);

  React.useEffect(() => {
    setIRequests_(RequestsState?.IRequests)
    
  }, [RequestsState]);


  React.useEffect(() => {
    if (RequestsState.message != "") {
      setAlertVisible(true);

      if (RequestsState.hasError) {
        setAlertType("danger");
      } else {
        setAlertType("success");
      }
      window.setTimeout(() => {
        setAlertVisible(false);
        dispatch(actions.ClearMeassageError());


      }, 2000)
    }
    else {
      setAlertVisible(false);
    }


  }, [RequestsState.message]);
  const onSubmit = async (data: any) => {
   let res= handleRowFunctionsref.current?.AssignTo(data);
   dispatch(actions.CreateRequestAdmin(userInfo?.userId));
   res? toast.success("  "):toast.error(" ");
  }
  return (
    <>
    {countOfAssigned.length>0 &&<HorizontalBarChart  data={countOfAssigned}/> } 
      <Alert color={alertType} isOpen={alertVisible} >
        {RequestsState?.message}
      </Alert>
      <form style={{padding:"50px"}} className="user" onSubmit={handleSubmit(onSubmit)}>
        {/* ################### form- row-004 #################*/}
        <div className="form-group row">
          <label htmlFor="adminUser" className="col-sm-2 col-form-label"></label>
          <div className="col-sm-4 d-flex">
            <span className="mt-2 " style={{ color: "#ff0000", marginLeft: "12px" }}>*</span>
            <Controller
              name="adminUser"
              control={control}
              placeholder="   "
              className="form-control form-control-user border-0"
              rules={{
                required: true
              }}
              options={exsistedUsers}
              as={Select}
            />

          </div>
          <div className="col-sm-3 d-flex">
            <button  disabled={RequestsState?.IRequests.length == 0} className="btn btn-primary btn-user  btn-block"></button>
          </div>
        </div>
      </form>
      {/* ################### form- row-005 #################*/}
      {(userInfo?.userId != 0 && IRequests_.length != 0) && <TableBootStrap hasSelection={true} ref={handleRowFunctionsref} data={IRequests_}  columns={columnsBootStrap_.columns} tableName="  " />}
      {(userInfo?.userId == 0 || IRequests_.length == 0) && <NoData Message="     " />}
      {/* {( IRequests.length !=0) && <TableBootStrap   ref={handleRowFunctionsref}  data={columnsBootStrap_.data} columns={columnsBootStrap_.columns}  />} */}

    </>
  );
}
