import { GET_NONSAPUSERS } from "./Urls";
import { INonSapUsers, IResponse } from "../types";


export const getNonSapUsersRegistered = async (): Promise<IResponse> => {

    let rtnObj: IResponse = {

    };
    try {
        let response = await fetch(GET_NONSAPUSERS, {
            method: 'GET',
            headers: {
                'Content-Type': 'application/json'
            }
        });
        if (!response.ok) {

        }
        rtnObj = await response.json();
    } catch (error) {
        rtnObj = {
            status: 1,
            message: "ErrorOccured",
            hasError: true
        }
    }

    return rtnObj;

}


export const updateNonSapUsersRegistered=async (data:INonSapUsers):Promise<IResponse>=>{

    let rtnObj: IResponse ={};
    let obj = {

        "userId": data.userId,
        "StatusId": data.RegistrationStatusId
    }

    try {
        let response= await fetch(GET_NONSAPUSERS+`/${data.userId}/${data.RegistrationStatusId}`,{
            method:"PUT",
            headers:{
                'Content-Type':'application/json'
            },
            body: JSON.stringify({
                ...obj
            })
            
        });
        if (!response.ok) {

        }
        rtnObj = await response.json();
    } catch (error) {
        rtnObj = {
            status: 1,
            message: "ErrorOccured",
        
            hasError: true
        }
        
    }
    return rtnObj;

}

