import React, { useEffect, useState } from 'react';
import { useSelector, useDispatch } from 'react-redux';
import { RootState } from '../../../State/rootReducer';
import { useHistory } from "react-router-dom";
import Switch from "react-switch";
import {getNotificationSettings,updateIsActive} from "../../../Services/NotificationSettings";
import {ApplicationTypes as AppTypes} from "../../../types/Enums"
import {  toast } from 'react-toastify';
import { IResponse,IApplicationType } from '../../../types';



const NotificationSettings = () => {

  const userData = useSelector<RootState, RootState["login"]>(state => state.login);
  const history = useHistory();
  let dispatch = useDispatch();

  const [chkResidencyRenewal, setchkResidencyRenewal] = useState(false);
  const [chkInformationTransfer, setchkInformationTransfer] = useState(false);
  const [chkUpdateData, setchkUpdateData] = useState(false);
  const [chkCancelResidency, setchkCancelResidency] = useState(false);
  const [chkResidencyExtension, setchkResidencyExtension] = useState(false);


  const [AcceptEmailNotification, setAcceptEmailNotification] = useState(false);
  const [RejectEmailNotification, setRejectEmailNotification] = useState(false);
  const [ReturnEmailNotification, setReturnEmailNotification] = useState(false);
  const [RegistEmailNotification, setRegistEmailNotification] = useState(false);
  const [PaymentEmailNotification, setPaymentEmailNotification] = useState(false);


  const [AcceptSmsNotification, setAcceptSmsNotification] = useState(false);
  const [RejectSmsNotification, setRejectSmsNotification] = useState(false);
  const [ReturnSmsNotification, setReturnSmsNotification] = useState(false);
  const [RegistSmsNotification, setRegistSmsNotification] = useState(false);
  const [PaymentSmsNotification,setPaymentSmsNotification] = useState(false);



  const onChange = async (checked: boolean, event: MouseEvent | React.SyntheticEvent<MouseEvent | KeyboardEvent, Event>, id: string) => {
     switch (String(id))
     {
         case "AcceptEmailNotification" :
         {
          setAcceptEmailNotification(checked);
          break;
         }
         case "RejectEmailNotification" :
         {
          setRejectEmailNotification(checked);
          break;
         }
         case "ReturnEmailNotification" :
         {
          setReturnEmailNotification(checked);
          break;
         }
         case "RegistEmailNotification" :
         {
          setRegistEmailNotification(checked);
          break;
         }
         case "PaymentEmailNotification" :
         {
          setPaymentEmailNotification(checked);
          break;
         }

          /// sms switch casses 
        
          case "AcceptSmsNotification" :
          {
           setAcceptSmsNotification(checked);
           break;
          }
          case "RejectSmsNotification" :
          {
           setRejectSmsNotification(checked);
           break;
          }
          case "ReturnSmsNotification" :
          {
           setReturnSmsNotification(checked);
           break;
          }
          case "RegistSmsNotification" :
          {
           setRegistSmsNotification(checked);
           break;
          }
          case "PaymentSmsNotification" :
          {
           setPaymentSmsNotification(checked);
           break;
          }
     }
     let res:IResponse<IApplicationType> = await updateIsActive(String(id),checked); 
     if (res.hasError){
       toast.error(res.message);
     }
     else{
      toast.success(res.message);
     }
     
  }


  useEffect(() => {
    const getIntialVal = async ()=>{
      let res:IResponse<any> = await getNotificationSettings();
        let values= res.response;
      if (!res.hasError){
               setAcceptEmailNotification(values.acceptEmailNotification as boolean);
               setRejectEmailNotification(values.rejectEmailNotification as boolean);
               setReturnEmailNotification(values.returnEmailNotification as boolean);
               setRegistEmailNotification(values.registEmailNotification as boolean);
               setPaymentEmailNotification(values.paymentEmailNotification as boolean);
               
               setAcceptSmsNotification(values.acceptSmsNotification as boolean);
               setRejectSmsNotification(values.rejectSmsNotification as boolean);
               setReturnSmsNotification(values.returnSmsNotification as boolean);
               setRegistSmsNotification(values.registSmsNotification as boolean);
               setPaymentSmsNotification(values.paymentSmsNotification as boolean);
      }
      else {
        toast.error(res.message);
      }
    }
    getIntialVal();
    
  }, [])




  return (
    <main className="login-bg">
     
     <div className="container"  style={{display:'flex',flexDirection:'row' }}> 
        {/* SMS Notifications */}
        <div className="row justify-content-center" style={{flex:'50%'}}>
          <div className="col-xl-10 col-lg-12 col-md-9">
            <div className="card o-hidden border-1 shadow my-5 animate__animated animate__backInDown " style={{ border: '1px solid rgb(189, 189, 189)' }}>
              <div className="card-header bg-dark">
                <div className="text-center">
                  <h1 className=" text-gray-900 mb-1 shorooq gold" style={{ fontSize: '28px' }}>  </h1>
                </div>
              </div>
              <div className="card-body p-0 ">
                {/* Nested Row within Card Body */}
                <div className="row">
                  <div className="col-lg-12">
                    <div className="p-5">
                      <form className="user" >
                        <div className="form-group row">
                          <label htmlFor="employeeType" className="col-sm-6 col-form-label">     </label>
                          <Switch
                            className="col-sm-6"
                            onChange={onChange}
                            checked={RegistSmsNotification}
                            id="RegistSmsNotification"
                          />
                        </div>
                        <div className="form-group row">
                          <label htmlFor="employeeType" className="col-sm-6 col-form-label">        </label>
                          <Switch
                            className="col-sm-6"
                            onChange={onChange}
                            checked={ReturnSmsNotification}
                            id="ReturnSmsNotification"
                          />
                        </div>
                        <div className="form-group row">
                          <label htmlFor="employeeType" className="col-sm-6 col-form-label">     </label>
                          <Switch
                           className="col-sm-6"
                            onChange={onChange}
                            checked={RejectSmsNotification}
                            id="RejectSmsNotification"
                          />
                        </div>
                        <div className="form-group row">
                          <label htmlFor="employeeType" className="col-sm-6 col-form-label">       </label>
                          <Switch
                            className="col-sm-6"
                            onChange={onChange}
                            checked={AcceptSmsNotification}
                            id="AcceptSmsNotification"
                          />
                        </div>
                        <div className="form-group row">
                          <label htmlFor="employeeType" className="col-sm-6 col-form-label">      </label>
                          <Switch
                            className="col-sm-6"
                            onChange={onChange}
                            checked={PaymentSmsNotification}
                            id="PaymentSmsNotification"
                          />
                        </div>
             
                      </form>
                    </div>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </div>
 
    
        {/* Email Notifications */}
        <div className="row justify-content-center" style={{flex:'50%'}}>
          <div className="col-xl-10 col-lg-12 col-md-9">
            <div className="card o-hidden border-1 shadow my-5 animate__animated animate__backInDown " style={{ border: '1px solid rgb(189, 189, 189)' }}>
              <div className="card-header bg-dark">
                <div className="text-center">
                  <h1 className=" text-gray-900 mb-1 shorooq gold" style={{ fontSize: '28px' }}>  </h1>
                </div>
              </div>
              <div className="card-body p-0 ">
                {/* Nested Row within Card Body */}
                <div className="row">
                  <div className="col-lg-12">
                    <div className="p-5">
                      <form className="user" >
                        <div className="form-group row">
                          <label htmlFor="employeeType" className="col-sm-6 col-form-label">      </label>
                          <Switch
                            className="col-sm-6"
                            onChange={onChange}
                            checked={RegistEmailNotification}
                            id="RegistEmailNotification"
                          />
                        </div>
                        <div className="form-group row">
                          <label htmlFor="employeeType" className="col-sm-6 col-form-label">           </label>
                          <Switch
                            className="col-sm-6"
                            onChange={onChange}
                            checked={ReturnEmailNotification}
                            id="ReturnEmailNotification"
                          />
                        </div>
                        <div className="form-group row">
                          <label htmlFor="employeeType" className="col-sm-6 col-form-label">     </label>
                          <Switch
                           className="col-sm-6"
                            onChange={onChange}
                            checked={RejectEmailNotification }
                            id="RejectEmailNotification"
                          />
                        </div>
                        <div className="form-group row">
                          <label htmlFor="employeeType" className="col-sm-6 col-form-label">        </label>
                          <Switch
                            className="col-sm-6"
                            
                            onChange={onChange}
                            checked={AcceptEmailNotification}
                            id="AcceptEmailNotification"
                          />
                        </div>
                        <div className="form-group row">
                          <label htmlFor="employeeType" className="col-sm-6 col-form-label">        </label>
                          <Switch
                            className="col-sm-6"
                            onChange={onChange}
                            disabled
                            checked={PaymentEmailNotification}
                            id="PaymentEmailNotification"
                          />
                        </div>
             
                      </form>
                    </div>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </div>
    
        </div>
     
      </main>



  )
}

export default NotificationSettings;
