import { GET_USERTYPES,GET_GETNONSAPUSER,GET_UPDATENONSAPUSER } from "./Urls";

export const getUpdateUserInfo = async (input: any) => {
    let response = await fetch(GET_GETNONSAPUSER+"?civilID="+input.civilId+"&name="+input.name, {
        method: "GET",
        headers: {
            'Content-Type': 'application/json'
        },
    });
    let res = await response.json()
    return res;
}

export const updateUpdateUserInfo = async (input: any) => {

    let response = await fetch(GET_UPDATENONSAPUSER, {
        method: "POST",
        headers: {
            'Content-Type': 'application/json'
        },
        body: JSON.stringify({ ...input })
    });
    let res = await response.json()
    return res;

}

export const deleteUpdateUserInfo = async (input: any) => {

    let response = await fetch(GET_GETNONSAPUSER, {
        method: "DELETE",
        headers: {
            'Content-Type': 'application/json'
        },
        body: JSON.stringify({ ...input })
    });
    let res = await response.json()
    return res;

}


