import { GET_USERTYPES } from "./Urls";

export const insertUserTypesSettings = async (input: any) => {

    let response = await fetch(GET_USERTYPES, {
        method: "POST",
        headers: {
            'Content-Type': 'application/json'
        },
        body: JSON.stringify({ ...input })
    });
    let res = await response.json()
    return res;

}

export const updateUserTypesSettings = async (input: any) => {

    let response = await fetch(GET_USERTYPES, {
        method: "PUT",
        headers: {
            'Content-Type': 'application/json'
        },
        body: JSON.stringify({ ...input })
    });
    let res = await response.json()
    return res;

}

export const deleteUserTypesSettings = async (input: any) => {
    let response = await fetch(GET_USERTYPES+"?input="+input?.input, {
        method: "DELETE",
        headers: {
            'Content-Type': 'application/json'
        },
        body: JSON.stringify({ ...input })
    });
    let res = await response.json()
    return res;

}


