

import * as actions from './action'
import * as types from "./types"

const initialState:types.IState={
    featureId1:undefined,
    featureId2:undefined,
    featureId3:undefined
}

export function generalSettingReducer(
    state:types.IState=initialState,action:types.generalStettingTypes):types.IState{
switch (action.type) {
 
    case types.GENERALSETTING_SUCCESS :
        return{
            ...state,
              featureId1:action.payload.featureId1,
              featureId2:action.payload.featureId2,
              featureId3:action.payload.featureId3
        }

    default:
        return state;
}

}