import React, { FC } from 'react'
import SideMenu from "./SideMenu";
import AuthHeader from "../Common/AuthHeader";
import Header from '../Common/Header';
import FullPageLoader from '../../Controls/FullPageLoader';
import { getLocalStorage } from '../../Services/utils/localStorageHelper';
import { useSelector,useDispatch } from "react-redux";
import { RootState } from "../../State/rootReducer";
import { authenticateResponse } from '../../types/userInfo';
import { ToastContainer, toast } from 'react-toastify';
import 'react-toastify/dist/ReactToastify.css';
import '../../assets/js/all';


const Layout: FC = ({ children }) => {
    const LookUpState = useSelector<RootState,RootState["lookUp"]>(state => state.lookUp);
    const {IsLoading} = LookUpState;
    const authenticationState= useSelector<RootState,RootState["login"]>(state=>state.login);
    return (
        <>
         <Header />
            <div id="layoutSidenav">
                <SideMenu />
                <div id="layoutSidenav_content">              
                    {authenticationState.isLoggedIn && 
                    <AuthHeader loggedIn={authenticationState.isLoggedIn} 
                    fullName={ authenticationState?.userInfo?.employeeName!=undefined?authenticationState?.userInfo?.employeeName:""}
                    civilId = { authenticationState?.userInfo?.civilId!=undefined?authenticationState?.userInfo?.civilId:""} />}
                    {children}
                </div>
            </div>
            { IsLoading && <FullPageLoader />}
            <ToastContainer  position="top-left" rtl={true}/>
        </>
    )
}

export default Layout;
