import React from 'react';
import PublicRoute from './PublicRoute';
import * as Pages from './../../Pages/index'
import { Route } from 'react-router-dom';

const Index = () => {
  return <Route>
  <Route exact path="/" component={Pages.PublicPages.Login.default} />
  <Route exact path="/Public/" component={Pages.PublicPages.Login.default} />
  <Route exact path="/Public/login" component={Pages.PublicPages.Login.default} />
  <Route exact path="/Public/CannotAccessThisSystem" component={Pages.PublicPages.CannotAccessThisSystem.default} />
  <Route exact path="/Public/CannotAccessThisPages" component={Pages.ClientSecPages.PassportInfo} />
  <Route exact path="/Public/register" component={Pages.PublicPages.Register.default} />
  <Route exact path="/Public/RegisterSuccess" component={Pages.PublicPages.RegisterSuccess.default} />
  </Route>;
};

export default Index;
