import {
  Loading,
  AppTypes_Success,
  Nationalities_Success,
  UserTypes_Success,
  LookUpActionTypes,
  ILookUpState,
  jobTypes_Success,
  organizationTypes_Success,AppTypes_Clear
} from './types';


const initialState: ILookUpState = {
  AppTypes: undefined,
  JobTypes: undefined,
  Nationalities: undefined,
  UserTypes: undefined,
  OrganizationTypes: undefined,
  IsLoading: false
}


export function lookUpReducer(state: ILookUpState = initialState,action: LookUpActionTypes): ILookUpState 
{
  switch (action.type) {
    case Loading:
      return {
        ...state,
        IsLoading: action.payload
      }
    case jobTypes_Success:
      return {
        ...state,
        JobTypes: [...action.payload]
      }
    case organizationTypes_Success:
      return {
        ...state,
        OrganizationTypes: [...action.payload]
      }
       
    case AppTypes_Success:
      return {
        ...state,
        AppTypes: [...action.payload]
      }
      case AppTypes_Clear :
      return{
        ...state,
        AppTypes:undefined
      }
    case Nationalities_Success:
      return {
        ...state,
        Nationalities: [...action.payload]
      }
    case UserTypes_Success:
      return {
        ...state,
        UserTypes: [...action.payload]
      }

    default:
      return state
  }

}

export default lookUpReducer;