import React from "react";
import { Redirect, Route } from "react-router-dom";

import { getLocalStorage } from "../../../Services/utils/localStorageHelper"

import { authenticateResponse } from "../../../types/userInfo";
import LayoutAdmin from "../../../Layout/ResidencySec/Employee/LayoutAdmin";
import { store } from "../../../index"
import { useSelector } from "react-redux";
import { RootState } from "../../../State/rootReducer";


const ProtectedRouteAdmin: React.ComponentType<any> = ({ component: Component, ...rest }) => {

	
	const authenticationState= useSelector<RootState,RootState["login"]>(state=>state.login);
	let callBack = store?.getState()?.router?.location?.pathname;

	return (
		<Route
			{...rest}
			render={(props) => {
				if (authenticationState.isLoggedIn === true) {

					if (( (authenticationState.userInfo?.userRoleId == 1 || authenticationState.userInfo?.userRoleId == 3) )) 
					{
						 return <LayoutAdmin><Component {...props} /></LayoutAdmin>; 
					
					}
					else
					{
						// redirect to you have no permission to access to this pages
						return (<Redirect
							to={{
								pathname: "/CannotAccessThisPages",
								state: { from: props.location },
							}}
						/>); 
					}


				}

				else {
					if(callBack!= undefined && callBack!== null) {
					return (<Redirect
						to={{
							pathname: "/Public/login",
							search: `${callBack}`
						}}
					/>);}
					else return (<Redirect
						to={{
							pathname: "/Public/login"
						}}
					/>); 

				}
			}}
		/>
	);
};

export default ProtectedRouteAdmin;