import * as loginActions from "./types"


export const selectUser=(data:any):loginActions.login_select_user_action_type=>
{
    return {
        type:loginActions.SELECTUSER,
        payload:data
    }
}
export const selectUserSuccess=(data:any):loginActions.login_select_user_success_action_type=>
{
    return {
        type:loginActions.SELECTUSERSUCCESS,
        payload:data
    }
}
export const clearSelectedUser=():loginActions.login_clear_selected_user_action_type=>
{
    return{
        type:loginActions.CLEARSELECTEDUSER
    }
}
export const getloginRequest = (Credentials: loginActions.credentials): loginActions.LoginActionTypes => {
    return {
        type: loginActions.LOGIN_REQUEST,
        payload: Credentials
    }
}
export const getloginRequestLoading = (): loginActions.login_loading_action_type => {
    return {
        type: loginActions.LOGIN_LOADING,
    }
}
export const getloginSuccess = (state: loginActions.AuthState): loginActions.LoginActionTypes => {
    return {
        type: loginActions.LOGIN_SUCCESS,
        payload: state
    }
}
export const getllogout = (): loginActions.logout_action_type => {
    return {
        type: loginActions.LOGOUT,
    }
}
export const changeProfile = (data: loginActions.IUserProfile): loginActions.profile_action_type => {
    return {
        type: loginActions.PROFILECHANGE,
        payload: data
    }
}
export const USERUPDATEINFO = (data: any): loginActions.UserUpdateInfo_action_type => {
    return {
        type: loginActions.USERUPDATEINFO,
        payload: data
    }
}

export const USERUPDATEINFOSUCCESS = (data: any): loginActions.UserUpdateInfoSuccess_action_type => {
    return {
        type: loginActions.USERUPDATEINFOSUCCESS,
        payload: data
    }
}