import { Button } from 'primereact/button';
import React, { ReactElement } from 'react'
import { Item } from 'react-bootstrap/lib/Breadcrumb';
import { getLdabUsersList, getAdminUsers, getUserRoleLookup, addUserToDbWithUserRoleIdCall, putUserRoleIdCall } from '../../../Services/userPermissions'
import { IResponse } from '../../../types';
import { Ilookup } from '../../../types/lookUpTypes';
import Select from 'react-select';
import { useForm, useController, Controller } from "react-hook-form"
import { toast } from 'react-toastify';
import { confirmAlert } from 'react-confirm-alert'; // Import
import FullPageLoader from "../../../Controls/FullPageLoader"

interface Props {

}

interface IUser {
    userName?: string;
    employeeName?: string;
    email?: string;
    civilId?: string;
    userRoleId?: number;
    employeeNumber?: string;
    userId?: number;
    userRoleName?: string;
    comeFromLdab?: boolean;
}



export default function UserManagement({ }: Props): ReactElement {

    const [showAddnewUser, setAddNewUser] = React.useState(false);
    const [exsistedUsers, setExsistedUsers] = React.useState<IUser[]>([]);
    const [restUsers, setRestUsers] = React.useState<IUser[]>([]);
    const [selectedUser, setSelectedUser] = React.useState<IUser>({});
    const [searchTextLdab, setSearchTextLdab] = React.useState<string>("");
    const [searchTextExist, setSearchTextExist] = React.useState<string>("");
    const [ldabUserResult, setLdabUserResult] = React.useState<IUser[]>([]);
    const [userRoleLookup, setUserRoleLookup] = React.useState<Ilookup[]>([]);
    const [buzy, setBuzy] = React.useState<boolean>(false);
    const { control, errors, reset, handleSubmit, register, setValue } = useForm();
    React.useEffect(() => {
        async function loadDatat() {
            let res: IResponse<IUser[]> = await getAdminUsers();
            setExsistedUsers(res.response as IUser[]);
            let resLookup: Ilookup[] = await getUserRoleLookup();
            setUserRoleLookup(resLookup as Ilookup[]);
        }
        loadDatat();
        setAddNewUser(true);
    }, []);
    React.useEffect(() => {
        async function loadDatat() {
            let res: IResponse<IUser[]> = await getAdminUsers();
            setExsistedUsers(res.response as IUser[]);
        }
        if (searchTextExist == "") {
            loadDatat();
        }
        let filteredArray = exsistedUsers.filter(r => r.employeeName?.match(searchTextExist));
        setExsistedUsers(filteredArray);
    }, [searchTextExist]);
    const onClickNewUserButton = () => {
        setAddNewUser(true);
        setLdabUserResult([]);
        setSearchTextLdab("");
    }
    const onClickEditUserButton = (input: IUser, Ldab: boolean) => {
        input.comeFromLdab = Ldab;
        setSelectedUser({});
        setSelectedUser(input);
        setAddNewUser(false);
    }
    const onClickLdabSearch = async () => {
        if (searchTextLdab != "") {
            let res: IResponse<IUser[]> = await getLdabUsersList(searchTextLdab);
            setLdabUserResult(res.response as IUser[]);
        }
        else
        {
            toast.error("    ");
        }
    }
   
    async function loadDatat() {
        let res: IResponse<IUser[]> = await getAdminUsers();
        setExsistedUsers(res.response as IUser[]);
        let resLookup: Ilookup[] = await getUserRoleLookup();
        setUserRoleLookup(resLookup as Ilookup[]);
    }
    const submitForm = async (inputForm: any) => {
        confirmAlert({
            customUI: ({ onClose }) => {
              return (
                <div style={{ textAlign: 'center', width: 500, borderRadius: 10, padding: 40, background: '#005da3', boxShadow: '0 20px 75px rgb(0 0 0 / 23%)', color: '#fff' }} >
                  <h4>       ?</h4>
                  <button style={{ margin: 20, width: 50, borderRadius: 10, fontSize: 20, background: 'white' }} onClick={() => {updateUserRole(inputForm,selectedUser);onClose() }}> </button>
                  <button style={{ margin: 20, width: 50, borderRadius: 10, fontSize: 20, background: 'white' }} onClick={() => { onClose() }}></button>
                </div>
              );
            }
          });
      
    }

   async function updateUserRole(inputForm:any,selectedUser:IUser)
    {
        setBuzy(true);
        if (selectedUser.comeFromLdab) {
            // save from ldab
              let user:IUser={userName:selectedUser.userName ,userRoleId:inputForm.userRole.value,civilId:selectedUser.civilId,employeeName:selectedUser.employeeName};
            let res: IResponse = await addUserToDbWithUserRoleIdCall(user);
            if (res.hasError) {
                toast.error(res.message);
            } else {
                if (res.message != null)
                  {  toast.success(res.message);
                    loadDatat();
                  
                }
            }
        }
        else {
            //update Exsisting User  
            let user:IUser={userId:selectedUser.userId ,userRoleId:inputForm.userRole.value};
            let res: IResponse = await putUserRoleIdCall(user);
            if (res.hasError) {
                toast.error(res.message);
            } else {
                if (res.message != null)
                   { toast.success(res.message);
                    loadDatat();
                   }
            }
        }
        setBuzy(false);


    }

    React.useEffect(() => {
        setValue("userRole", (userRoleLookup as Ilookup[])?.find(j => j.value === '' + selectedUser.userRoleId));
    }, [selectedUser]);
    const keyUpIgnore=(e:any)=>{
        if(e.keyCode==13)
        return false;
    }

    return (
        <>
       {buzy&&<FullPageLoader/>} 
            <div className="container" style={{ marginBottom: "10%" }}>
                ;<div
                    className="bg-white p-3 mt-5 o-hidden shadow my-5 animate__animated animate__backInDown "
                    style={{ marginBottom: 80, border: "1px solid rgb(189, 189, 189)" }}
                >
                    {/* Outer Row */}
                    <div className="row justify-content-center" >
                        <div className="col-xl-4 col-lg-4 col-md-4 border-left">
                            <div className="card border-0 pt-5">
                                <div className="float-left border-bottom mb-3">
                                    <h3 className="text-gray-900 mb-1 shorooq gold">
                                         {" "}
                                        <Button
                                            onClick={() => onClickNewUserButton()}
                                            className="btn btn-warning d-inline-block float-left"
                                           
                                            style={{fontSize:"12px",fontWeight:"bold"}}
                                        >
                                            <i className="fa fa-ok "></i>
                                            {/* <i class="fas fa-plus"></i> */}
                                           

                                        </Button>
                                    </h3>
                                </div>
                                <div className="card-body p-0 ">
                                    {/* Nested Row within Card Body */}
                                    <div className="row">
                                        <div className="col-lg-12">
                                            <div>
                                                <form className="user">
                                                    {/* ################### form- row-001 #################*/}
                                                    <div className="form-group row">
                                                        {/* <label for="" class="col-sm-3 col-form-label"> </label> */}
                                                        <div className="col-sm-12">
                                                            <input
                                                                type="text"
                                                                className="form-control form-control-user"
                                                                placeholder="..."
                                                                onChange={e => setSearchTextExist(e.target.value)}
                                                                onKeyUp={(e)=> keyUpIgnore(e) }
                                                            />
                                                        </div>
                                                    </div>
                                                </form>
                                                <ul className="list-block vh-50 pre-scrollable">
                                                    {exsistedUsers!=null&& exsistedUsers.map((item: IUser) =>


                                                        <li>
                                                            <a style={{ cursor: "pointer" }} onClick={() => onClickEditUserButton(item, false)} data-id="" >{item.employeeName}</a>
                                                        </li>
                                                    )}


                                                </ul>
                                                <br />
                                            </div>
                                        </div>
                                    </div>
                                </div>
                            </div>
                        </div>
                        <div className="col-xl-8 col-lg-8 col-md-8">
                            {/*  First tab   */}

                            <div
                                className="card border-0 pt-5"
                                style={{ border: "1px solid rgb(189, 189, 189)", display: showAddnewUser == false ? "block" : "none" }}
                            >
                                <div className="text-left border-bottom">
                                    <h1
                                        className=" text-gray-900 shorooq gold pl-3 pb-3"
                                        style={{ fontSize: 28 }}
                                    >
                                         
                                    </h1>
                                </div>
                                <div className="card-body p-0 ">
                                    {/* Nested Row within Card Body */}
                                    <div className="row">
                                        <div className="col-lg-12">
                                            <div className="p-3">
                                                <form className="user" onSubmit={handleSubmit(submitForm)}>
                                                    {/* ################### form- row-002 #################*/}
                                                    <div className="form-group row">
                                                        <label className="col-sm-3 col-form-label">
                                                             -
                                                        </label>
                                                        <div className="col-sm-6">
                                                            <input
                                                                type="text"
                                                                disabled
                                                                name="employeeName"
                                                                className="form-control form-control-user"
                                                                value={selectedUser.employeeName}
                                                                ref={register()}
                                                            />
                                                        </div>

                                                    </div>
                                                    {/* ################### form- row-002 #################*/}
                                                    <div className="form-group row">
                                                        <label className="col-sm-3 col-form-label">
                                                              
                                                        </label>
                                                        <div className="col-sm-6">
                                                            <input
                                                                type="text"
                                                                name="userName"
                                                                disabled
                                                                className="form-control form-control-user"
                                                                value={selectedUser.comeFromLdab ? selectedUser.userName : selectedUser.email}
                                                                ref={register()}
                                                            />
                                                        </div>
                                                    </div>
                                                    {/* ################### form- row-003 #################*/}
                                                    <div className="form-group row">
                                                        <label className="col-sm-3 col-form-label">
                                                             
                                                        </label>
                                                        <div className="col-sm-6">
                                                            <input
                                                                type="text"
                                                                name="civilId"
                                                                disabled
                                                                className="form-control form-control-user"
                                                                value={selectedUser.civilId}
                                                                ref={register()}
                                                            />
                                                        </div>
                                                    </div>
                                                    <div className="form-group row">

                                                        <label className="col-sm-3 col-form-label">
                                                             
                                                        </label>
                                                        <div className="col-sm-6">
                                                            <Controller
                                                                name="userRole"
                                                                control={control}
                                                                className="form-control form-control-user border-0"
                                                                placeholder="    "
                                                                options={userRoleLookup}
                                                                ref={register()}
                                                                as={Select}
                                                            />
                                                        </div>
                                                    </div>
                                                    <hr />

                                                    {/* ################# submit btn ##################### */}
                                                    <div className="row justify-content-center">
                                                        {" "}
                                                        <Button
                                                            className="btn btn-primary btn-user shorooq mr-2"
                                                            style={{ fontSize: 22 }}
                                                            type="submit"
                                                        >
                                                            
                                                        </Button>
                                                        {" "}
                                                        <a
                                                            className="btn btn-warning btn-user shorooq mr-2"
                                                            style={{ fontSize: 22 , cursor:"pointer" }}
                                                            onClick={() => onClickNewUserButton()}
                                                        >
                                                            
                                                        </a>

                                                    </div>

                                                    {/* ################# end submit btn ##################### */}
                                                </form>
                                            </div>
                                        </div>
                                    </div>
                                </div>
                            </div>
                            {/*  second tab   */}
                            <div

                                className="card border-0 pt-5"
                                style={{ border: "1px solid rgb(189, 189, 189)", display: showAddnewUser == true ? "block" : "none" }}
                            >
                                <div className="text-left border-bottom">
                                    <h1
                                        className=" text-gray-900 shorooq gold pl-3 pb-3"
                                        style={{ fontSize: 28 }}
                                    >
                                          
                                    </h1>
                                </div>
                                <div className="card-body p-0 ">
                                    {/* Nested Row within Card Body */}
                                    <div className="row">
                                        <div className="col-lg-12">
                                            <div className="p-3">
                                                <form className="user">
                                                    {/* ################### form- row-002 #################*/}
                                                    <div className="input-group mb-3">
                                                        <input
                                                            type="text"
                                                            className="form-control"
                                                            placeholder="..."
                                                            aria-label="Example text with button addon"
                                                            aria-describedby="button-addon1"
                                                            value={searchTextLdab}
                                                            onChange={e => setSearchTextLdab(e.target.value)}
                                                              onKeyUp={()=>onClickLdabSearch()}
                                                        />
                                                        <Button
                                                            className="btn btn-warning btn-user shorooq"
                                                            style={{ borderRadius: "0%" }}
                                                            type="button"
                                                            id="button-addon1"
                                                            onClick={() => onClickLdabSearch()}
                                                        >
                                                            
                                                        </Button>
                                                    </div>
                                                    <table className="table table-bordered text-center datatable">
                                                        <tbody>
                                                            <tr>
                                                                <td> </td>
                                                                <td> </td>
                                                                <td> </td>
                                                                <td> </td>
                                                            </tr>

                                                            {ldabUserResult &&ldabUserResult.map((item: IUser) => <tr> <td>{item.employeeName}</td> <td>{item.civilId}</td> <td>{item.userName}</td> <td>   <a  onClick={() => onClickEditUserButton(item, true)} className="btn btn-primary btn-user shorooq  " style={{ fontSize: 22,cursor: "pointer" }} >  </a> </td></tr>)}


                                                        </tbody>
                                                    </table>
                                                    {/* ################# end submit btn ##################### */}
                                                </form>
                                            </div>
                                        </div>
                                    </div>
                                </div>
                            </div>

                        </div>
                    </div>
                </div>
            </div>

        </>

    )
}
