  export const updateNewAppStatus = async (data:any): Promise<any> => {

    
    let res :IState ={};
    try {
      let url = `${NEWAPP_UPDATE}${data.userId}/${data.applicationNumber}/${data.statusId}?Remark='${data.remark}&SubmittedBy=${data.submittedBy}`;
      let response = await fetch(url, {
        method: 'PUT',
        headers: {
          'Content-Type': 'application/json'
        },
        body: JSON.stringify({
          ...data
         })

      });
      
      if (!response.ok) {
        // return { ...authenticateResponse, status: response.status, message: response.statusText, hasError: true }
      }

      let temp_=  await response.json();
      let temp:IStateApp ={IState:temp_.response,hasError:temp_.hasError,message:temp_.message,isloading:false};
      return temp;
  
    } catch (e) {
      return {IState:{},isloading:false,message:e,hasError:true}
    }
  } 


